<template>
  <div>
    <BreadCrumb class="bread" :breadData="breadData"></BreadCrumb>
    <div class="content">
      <p class="title">商家权限设置</p>
      <div class="add_menu">
        <el-button type="primary" size="mini" @click="addVisible = true">添加菜单</el-button>
      </div>
      <div class="contain">
        <p class="c_title">商家页面管理</p>
        <div class="c_content">
          <!--<el-checkbox-group class="check_group" v-model="checkedJurisdiction" @change="handleCheckedChange">
            <el-checkbox v-for="item in jurisdictions" :label="item.id" :key="item.id">
              <div class="e_check" @dblclick="addVisible = true">
                <p class="r_p1">{{item.name}}</p>
                <p class="r_p2">{{item.urls}}</p>
              </div>
            </el-checkbox>
          </el-checkbox-group>-->
          <div class="check_group">
            <div class="e_check" @dblclick="addVisible = true" v-for="item in jurisdictions" :label="item.id" :key="item.id">
              <p class="r_p1">{{item.name}}</p>
              <p class="r_p2">{{item.urls}}</p>
              <i class="iconfont icon-minus-square-fill icon_delete"></i>
            </div>
          </div>
        </div>
      </div>
    </div>
    <el-dialog :visible.sync="addVisible" width="400px">
      <div slot="title" class="dialog_head_title">
        <i class="iconfont icon-edit-square examine_icon"></i>
        <span>添加菜单</span>
      </div>
      <div class="jurisdiction_content">
        <div class="jurisdiction_content_item">
          <label class="el_lbl">菜单名称:</label>
          <el-input class="el_input" size="mini"></el-input>
        </div>
        <div class="jurisdiction_content_item">
          <label class="el_lbl">页面路径:</label>
          <el-input class="el_input" size="mini"></el-input>
        </div>
        <div class="jurisdiction_content_item">
          <label class="el_lbl">菜单介绍:</label>
          <el-input placeholder="30字内介绍" class="el_input_text_area" type="textarea" :autosize="{ minRows: 3, maxRows: 3}"></el-input>
        </div>
      </div>
      <div class="user_footer" slot="footer">
        <el-row class="row_e">
          <el-button class="btn_deletes" size="small" @click="addVisible = false">取消</el-button>
          <el-button type="primary" size="small"  class="btn_delete">保存</el-button>
        </el-row>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  const c = [];
  for (let k=0;k<20;k++){
    c.push({
      id: k,
      name: '停止报名',
      urls: 'Product/edit'
    })
  }
import BreadCrumb from '@/components/public/BreadCrumb';
export default {
  name: "ExternalBusinessJurisdiction",
  components: {
    BreadCrumb
  },
  data() {
    return {
      addVisible: false,
      breadData: [{
        id: 1,
        name: '外部管理',
        urls: '/index',
        icon: 'icon-home'
      }, {
        id: 2,
        name: '商家权限设置',
        urls: '/index/test',
        icon: 'icon-home'
      }],
      jurisdictions: c,
      checkedJurisdiction:[0,1]
    }
  },
  methods: {
    handleCheckedChange: function (value) {
      console.log(value)
    }
  }
}
</script>

<style scoped lang="less">
  .bread {
    margin: 10px;
  }
  .content {
    background: white;
    margin-left: 10px;
    margin-right: 10px;
    height: calc(100vh - 87px);
    width: calc(100vw - 221px);
    border-radius: 2px;
  }
  .title{
    text-align: left;
    padding: 10px;
    padding-left: 20px;
    font-size: 14px;
    border-bottom: 1px solid #f2f2f2;
  }
  .contain{
    margin: 20px;
    .c_title{
      text-align: center;
      color: white;
      background: #15bafe;
      font-size: 14px;
      height: 30px;
      line-height: 30px;
    }
    .c_content{
      height: 400px;
      border: 1px solid #f2f2f2;
      overflow-y: auto;
      .check_group{
        display: flex;
        flex-wrap: wrap;
      }
      .e_check{
        display: flex;
        color: white;
        height: 28px;
        line-height: 28px;
        font-size: 12px;
        margin: 20px 0 0 20px;
        .r_p1{
          background: #15bafe;
          border-right: 1px solid white;
          padding: 0 10px;
        }
        .r_p2{
          background: #15bafe;
          padding: 0 10px;
        }
        .r_p3{
          background: #ff5858;
          padding: 0 10px;
        }
        .icon_delete{
          color: #ff5858;
          font-size: 35px;
        }
      }
    }
  }
  .add_menu{
    text-align: left;
    margin-top: 20px;
    margin-left: 20px;
  }
  .dialog_head_title{
    text-align: start;
    font-size: 14px;
    border-bottom: 1px solid #f2f2f2;
    padding-bottom: 10px;
    .examine_icon{
      color: #15bafe;
    }
  }
  .jurisdiction_content{
    .jurisdiction_content_item{
      display: flex;
      line-height: 28px;
      margin-left: 20px;
      margin-bottom: 10px;
      .el_lbl{
        width: 60px;
      }
      .el_input{
        width: 240px;
        margin-left: 20px;
      }
      .el_input_text_area{
        width: 240px;
        margin-left: 20px;
      }
    }
  }
  .row_e{
    text-align: center;
    .btn_deletes{
      padding: 5px 15px;
      margin-right: 10px;
    }
    .btn_delete{
      padding: 5px 15px;
    }
  }
</style>
<style lang="less">
  .el-dialog__body{
    padding: 0 20px !important;
  }
  .check_group{
    .el-checkbox{
      margin-left: 30px !important;
    }
  }
</style>



