<template>
    <div class="menus">
      <div class="menu_left">
        <div class="left_content">
          <p class="left_title">现有菜单</p>
          <menuTrees
            class="menu_trees"
            :children="treeData.children"
            :depth="0"
            :label="treeData.name"
            :id="treeData.id"
            @ee="getMenu"
          ></menuTrees>
        </div>
      </div>
      <div class="menu_right">
        <p class="right_title">{{menu_name}}菜单下的权限</p>
        <div class="item_contain" :style="menuVisible?{display:'block'}:{display:'none'}">
          <div class="jurisdiction_content">
            <el-checkbox class="all_check" :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
            <div style="margin: 15px 0;"></div>
            <el-checkbox-group class="check_group" v-model="checkedJurisdiction" @change="handleCheckedCitiesChange">
              <el-checkbox class="check_item" v-for="item in jurisdictionData" :label="item" :key="item.id">
                <span class="check_lab">{{item.name}}<span class="line"></span>{{item.method}}</span>
              </el-checkbox>
            </el-checkbox-group>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
  const jurisdictions = [{
    id: 1,
    name: '基本信息',
    description: '电视剧的方式开始看',
    method: 'system/basic',
    type: 1
  },{
    id: 2,
    name: '删除',
    description: '电视剧的方式开始看',
    method: 'system/basic',
    type: 1
  },{
    id: 3,
    name: '修改',
    description: '电视剧的方式开始看',
    method: 'system/basic',
    type: 1
  },{
    id: 4,
    name: '添加备注',
    description: '电视剧的方式开始看',
    method: 'system/basic',
    type: 1
  },{
    id: 5,
    name: '驳回',
    description: '电视剧的方式开始看',
    method: 'system/basic',
    type: 1
  },{
    id: 6,
    name: '基本信息',
    description: '电视剧的方式开始看',
    method: 'system/basic',
    type: 1
  },{
    id: 7,
    name: '删除',
    description: '电视剧的方式开始看',
    method: 'system/basic',
    type: 1
  },{
    id: 8,
    name: '修改',
    description: '电视剧的方式开始看',
    method: 'system/basic',
    type: 1
  },{
    id: 9,
    name: '添加备注',
    description: '电视剧的方式开始看',
    method: 'system/basic',
    type: 1
  },{
    id: 10,
    name: '驳回',
    description: '电视剧的方式开始看',
    method: 'system/basic',
    type: 1
  }];
export default {
  name: "Jurisdiction",
  components: {
    menuTrees: {
      name: 'tree-menu',
      template: `
            <div class="tree-menu">
              <div class="label-wrapper">
                <div :style="indent" class="parent_menu">
                <div class="menu_label"  @click="toggleChildren(id,label)">
                  <i v-if="children" class="iconfont icon_left" :class="showChildren?'icon-jian-xianxingfangkuang':'icon-jia-xianxingfangkuang'"></i>
                  <span :style="depth===0?{background:'#1A67AB'}:depth===1?{background:'#1E86E5'}:depth===2?{background:'#4DACFE'}:{background:'#26C0D5'}" class="lbl_menu">{{ label }}</span>
                </div>
                  <!--<i class="iconfont icon-plus-square-fill icon_add" @click="openMenuModal(id)"></i>
                  <i class="iconfont icon-minus-square-fill icon_delete" @click="deleteMenu(id)"></i>
                  <i class="iconfont icon-edit-square icon_delete" @click="editMenu(id)"></i>-->
                </div>
              </div>
              <tree-menu
                v-if="showChildren"
                v-for="item in children"
                :children="item.children"
                :label="item.name"
                :depth="depth + 1"
                :key="item.id"
                :id="item.id"
                @ee="xx"
              >
              </tree-menu>
            </div>
          `,
      props: ['children', 'label', 'depth','id'],
      data() {
        return {
          showChildren: false,
          menu_visible: false,
          menu_id: null,
          tab_index:null,
          menu_name: ''
        }
      },
      computed: {
        indent() {
          return {transform: `translate(${this.depth * 20}px)`}
        }
      },
      methods: {
        toggleChildren(id,label) {
          this.showChildren = !this.showChildren;
          this.menu_visible = true;
          this.menu_id = id;
          this.tab_index = id;
          this.menu_name = label;
          this.$emit('ee',{id:this.menu_id,menu_visible:this.menu_visible,menu_name:label});
          this.$store.dispatch('getMenuInfo',{id:this.menu_id,menu_visible:this.menu_visible,menu_name:this.menu_name}).then(res=>{
            /*console.log(333,this.$store.state.menu.menu)*/
          });
        },
        openMenuModal: function (id) {
          /*console.log(id)*/
          this.menu_visible = true;
          this.menu_id = id;
          this.tab_index = id;
          this.$emit('ee',{id:this.menu_id,menu_visible:this.menu_visible});
          this.$store.dispatch('getMenuInfo',{id:this.menu_id,menu_visible:this.menu_visible}).then(res=>{
            console.log(333,this.$store.state.menu.menu)
          });

          /*console.log(111,this.menu_visible,this.menu_id )*/
        },
        deleteMenu: function (id) {
          console.log(id)
        },
        editMenu: function (id) {
          console.log(id)
        },
        xx: function () {
          /*console.log(111,this.menu_id,this.menu_visible)*/
          this.$emit('ee',{id:this.menu_id,menu_visible:this.menu_visible,menu_name:this.menu_name});
        }
      }
    }
  },
  data () {
    return {
      show: false,
      typeTitle: '',
      index: 0,
      menuVisible: true,
      menu_id: null,
      menu_name: '',
      treeData: {
        id: 0,
        name: 'root',
        children: [{
          id: 1,
          name: '菜单1',
          children: [{
            id: 11,
            name: '菜单11'
          }, {
            id: 12,
            name: '菜单12'
          }]
        }, {
          id: 2,
          name: '菜单2',
          children: [{
            id: 21,
            name: '菜单21'
          }, {
            id: 22,
            name: '菜单22'
          }]
        }],
      },
      checkAll: false,
      checkedJurisdiction: [],
      jurisdictionData: jurisdictions,
      isIndeterminate: true
    }
  },
  methods: {
    getMenu: function (data) {
      /*console.log('data1',this.$store.state.menu.menu)*/
      setTimeout(()=>{
        let childrenData = this.$store.state.menu.menu;
        this.menuVisible = childrenData.menu_visible;
        this.menu_id = childrenData.id;
        this.menu_name = this.$store.state.menu.menu.menu_name;
        /*console.log('data2',this.menu_name)*/
        this.$emit('ee',{id:this.menu_id,menu_visible:this.menu_visible,checkedJurisdiction:this.checkedJurisdiction})
        /*console.log(childrenData,this.menu_id,this.menuVisible)*/
      })
    },
    handleCheckAllChange(val) {
      console.log(val)
      this.checkedJurisdiction = val ? jurisdictions : [];
      console.log(this.checkedJurisdiction)
      this.isIndeterminate = false;
      this.$emit('ee',{id:this.menu_id,menu_visible:this.menu_visible,checkedJurisdiction:this.checkedJurisdiction})
    },
    handleCheckedCitiesChange(value) {
      console.log(value)
      let checkedCount = value.length;
      this.checkAll = checkedCount === this.jurisdictionData.length;
      this.isIndeterminate = checkedCount > 0 && checkedCount < this.jurisdictionData.length;
      this.$emit('ee',{id:this.menu_id,menu_visible:this.menu_visible,checkedJurisdiction:this.checkedJurisdiction})
    }
  }
}
</script>

<style scoped la lang="less">
.menus{
  display: flex;
  width: 100%;
  .menu_left{
    width: 50%;
    display: -webkit-box;
    .left_content{
      border: 1px solid #f2f2f2;
      width: 90%;
      display: block;
      .left_title{
        background: #409EFF;
        height: 28px;
        line-height: 28px;
        text-align: center;
        color: white;
      }
      .menu_trees{
        margin-top: 10px;
      }
    }

  }
  .menu_right{
    width: 50%;
    .right_title{
      background: #409EFF;
      height: 28px;
      line-height: 28px;
      text-align: center;
      color: white;
    }
    .item_contain{
      border: 1px solid #f2f2f2;
      padding: 20px;
      .items{
        display: flex;
        font-size: 13px;
        height: 28px;
        line-height: 28px;
        margin-bottom: 20px;
        .item_lb{
          width: 80px;
          text-align: left;
        }
        .item_input{
          width: 200px;
        }
      }
    }
    max-height: 400px;
    overflow-y: auto;
    .all_check{
      width: 100%;
      text-align: -webkit-left;
    }
    .check_group{
      .check_item{
        display: -webkit-box;
        margin-bottom: 15px;
        text-align: left;
        .check_lab{
          background: #409EFF;
          color: white;
          padding: 5px 10px;
          border-radius: 2px;
          .line{
            height: 10px;
            border-left: 1px solid #ffffff;
            margin: 0 10px;
          }
        }
      }
    }
  }
}
</style>
<style>
  .tree-menu{
    display: table;
    cursor: pointer;
  }
  .menu_label{

  }
  .lbl_menu{
    color: white;
    padding: 2px 15px;
  }
  .label-wrapper{
    margin-bottom: 10px;
  }
  .el-checkbox+.el-checkbox{
    margin-left: 0;
  }
</style>



