              <div class="demo-input-suffix">
                用户账号：
                <el-input v-model="userId"></el-input>
              </div>
              <div class="demo-input-suffix">
                身份证号：
                <el-input v-model="idnumber"></el-input>
              </div>
              <div class="demo-input-suffix">
                真实姓名：
                <el-input v-model="realName"></el-input>
              </div>
        </el-dialog>
    </div>
</template>

