            contact:"牛逼了",
            phone:"1234655684",
            ticketsNumber:500,
            unitPrice:10000,
            usedTicket:900,
            returnTicket:0,
            buyTicketsTime:"2018-06-20",
            endTime:"2019-06-50",
            state:"已结束"
          },]
        }
