<template>
  <div>
    <BreadCrumb class="bread" :breadData="breadData"></BreadCrumb>
    <div class="content">
      <p class="title">活动审批</p>
      <div class="content_contain">
        <div class="conditions">
          <el-select size="mini" class="select_normal" v-model="typeValue" placeholder="类型">
            <el-option
              v-for="item in typeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          <el-select size="mini" class="select_normal" v-model="plateValue" placeholder="板块">
            <el-option
              v-for="item in plateOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          <el-select size="mini" class="select_normal" v-model="levelValue" placeholder="二级">
            <el-option
              v-for="item in levelOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          <el-input
            class="select_normal"
            placeholder="联系人"
            size="mini"
            suffix-icon="el-icon-search"
            v-model="user_name">
          </el-input>
          <el-input
            class="select_normal"
            placeholder="活动名称"
            size="mini"
            suffix-icon="el-icon-search"
            v-model="activity_name">
          </el-input>
          <el-input
            class="select_normal"
            placeholder="电话"
            size="mini"
            suffix-icon="el-icon-search"
            v-model="user_phone">
          </el-input>
        </div>
        <div class="tables">
          <el-table
            :data="tableData"
            border
            :stripe="true"
            :header-row-style="{height:'40px'}"
            :header-cell-style="{padding:0,background:'#15bafe',color:'white'}"
            :row-style="{height:'40px'}"
            :cell-style="{padding:0}"
            style="width: 100%">
            <el-table-column
              prop="id"
              label="ID"
              width="50">
            </el-table-column>
            <el-table-column
              label="活动名称"
              min-width="200">
              <template slot-scope="scope">
                <span class="row_activity">{{scope.row.activityName}}</span>
              </template>
            </el-table-column>
            <el-table-column
              prop="companyName"
              label="公司对外名称"
              width="200">
            </el-table-column>
            <el-table-column
              prop="type"
              label="类型"
              width="80">
            </el-table-column>
            <el-table-column
              prop="plate"
              label="板块"
              width="80">
            </el-table-column>
            <el-table-column
              prop="level"
              label="二级"
              width="80">
            </el-table-column>
            <el-table-column
              prop="address"
              label="城市"
              width="120">
            </el-table-column>
            <el-table-column
              label="操作"
              width="240"
              fixed="right">
              <template slot-scope="scope">
                <el-button size="small" type="text">预览活动</el-button>
                <el-button size="small" @click="examineVisible = true" type="text">查看协议</el-button>
                <el-button size="small" type="text" @click="rejectVisible = true">驳回</el-button>
                <el-button size="small" type="text" @click="remarkVisible = true">添加备注</el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
    </div>
    <el-dialog :visible.sync="examineVisible" width="400px">
      <div slot="title" class="dialog_head_title">
        <i class="iconfont icon-faxian examine_icon"></i>
        <span>查看协议</span>
      </div>
      <div class="examine_content">
        <div class="examine_list">
          <div class="examine_text" v-for="(item,index) in examineData" :key="index">
            <a class="examine_text" href="http://cdn.kushualab.com/0E2B038E-FD7D-4883-955C-4D900B6A7A25.png?attname=">
              <p class="examine_down_p">{{index}}.{{' '}}{{item.name}}</p>
              <i class="icon_examine el-icon-download"></i>
            </a>
          </div>
        </div>
      </div>
      <div class="img_content">
        <div class="img_items">
          <div v-for="(item,index) in imgData" :key="index"  :class="index%3===0?'img_item1':'img_item'">
            <a href="http://cdn.kushualab.com/0E2B038E-FD7D-4883-955C-4D900B6A7A25.png?attname=">
              <img class="img" :src="item.img_urls">
              <div class="modal_img">
                <i class="el-icon-download modal_icon"></i>
              </div>
            </a>
          </div>
        </div>
      </div>
      <div class="user_footer" slot="footer">
        <el-row>
          <el-button class="btn_foot" @click="examineVisible = false">取消</el-button>
          <el-button type="primary" class="btn_foot">提交</el-button>
        </el-row>
      </div>
    </el-dialog>
    <el-dialog :visible.sync="rejectVisible" width="360px">
      <div slot="title" class="dialog_head_title">
        <i class="iconfont icon-adduser examine_icon"></i>
        <span>驳回</span>
      </div>
      <div class="reject_content">
        <p class="reject_p">驳回理由</p>
        <el-input class="reject_input" clearable size="mini"/>
        <p class="reject_p1">驳回类别选择</p>
        <div class="btn_group">
          <el-button @click="handleReject(item.id)" v-for="item in rejectData" :key="item.id" size="mini" :class="item.id===rejectValue?'btns_s':'btns'">
            {{item.name}}
          </el-button>
        </div>
      </div>
      <div class="user_footer" slot="footer">
        <el-row class="row_e">
          <el-button class="btn_deletes" size="small" @click="rejectVisible = false">取消</el-button>
          <el-button type="primary" size="small"  class="btn_delete">提交</el-button>
        </el-row>
      </div>
    </el-dialog>
    <el-dialog :visible.sync="remarkVisible" width="400px">
      <div slot="title" class="dialog_head_title">
        <i class="iconfont icon-edit-square examine_icon"></i>
        <span>添加备注</span>
      </div>
      <div class="remark_content">
        <p class="remark_p">内容</p>
        <el-input
          type="textarea"
          :rows="2"
          class="text_area"
          placeholder="商家的修改，取消，结束活动都将显示在历史里面"
          v-model="remark">
        </el-input>
        <p class="history_p">历史</p>
        <div class="h_content">
          <div class="history_item" v-for="(item,index) in historyData" :key="index">
            <div class="item_top">
              <div class="point"/>
              <p class="h_name">{{item.name}}</p>
              <p class="h_time">{{item.createTime}}</p>
            </div>
            <p class="h_message">{{item.message}}</p>
          </div>
        </div>
      </div>
      <div class="user_footer" slot="footer">
        <el-row class="row_e">
          <el-button class="btn_deletes" size="small" @click="remarkVisible = false">取消</el-button>
          <el-button type="primary" size="small"  class="btn_delete">提交</el-button>
        </el-row>
      </div>
    </el-dialog>
  </div>
</template>

