<style scoped lang="less">
  .bread{
    margin: 10px;
  }
  .content{
    background: white;
    margin-left: 10px;
    margin-right: 10px;
    height: calc(100vh - 87px);
    width: calc(100vw - 221px);
    border-radius: 2px;
  }
  .title{
    text-align: left;
    padding: 10px;
    padding-left: 20px;
    font-size: 14px;
    border-bottom: 1px solid #f2f2f2;
  }
  .conditions{
    display: flex;
    margin-left: 10px;
    margin-top: 20px;
  }
  .select_normal{
    width: 150px;
    margin-right: 10px;
  }
  .tables{
    margin: 20px 10px;
  }
  .dialog_head_title{
    text-align: start;
    font-size: 14px;
    border-bottom: 1px solid #f2f2f2;
    padding-bottom: 10px;
  }
  .examine_icon{
    color: #15bafe;
  }
  .examine_content{
    height: 120px;
    border: 1px solid #f2f2f2;
    .examine_text{
      display: flex;
      justify-content: space-between;
      margin-left: 10px;
      margin-bottom: 10px;
      cursor: pointer;
      a{
        text-decoration:none;
        width: 100%;
      }
      .examine_down_p{
        width: 250px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        font-size: 12px;
        color: #15bafe;
      }
      .icon_examine{
        margin-right: 10px;
        color: #404040;
      }
    }
    .examine_list{
      height: 100px;
      margin-top: 10px;
      overflow: auto;
    }
  }
  .img_content{
    height: 120px;
    border: 1px solid #f2f2f2;
    margin-top: 10px;
    .img_items{
      display: flex;
      flex-wrap: wrap;
      height: 100px;
      overflow-y: auto;
      .img_item{
        width: 28%;
        margin-right: 4%;
        margin-top: 10px;
        .img{
          width: 100%;
          height: 80px;
        }
      }
      .img_item1{
        width: 28%;
        margin-left: 4%;
        margin-right: 4%;
        margin-top: 10px;
        .img{
          width: 100%;
          height: 80px;
        }
      }
      .modal_img{
        position: relative;
        top: 0;margin-top: -83px;
        left: 0;
        width: 100%;
        height: 80px;
        background: rgba(101, 101, 101, 0.6);
        color: #ffffff;
        opacity: 0;
      }
      .modal_img:hover{
        opacity: 1;
      }
      .modal_icon{
        position: relative;
        font-size: 18px;
        top: 50% - 9px;
      }
    }
  }
  .remark_content{
    position: relative;
    top: -10px;
  }
  .remark_p{
    text-align: left;
  }
  .text_area{
    margin-top: 8px;
  }
  .history_p{
    text-align: left;
    margin-top: 8px;
    border-bottom: 1px solid #f2f2f2;
    padding-bottom: 10px;
  }
  .h_content{
    margin-top: 10px;
    height: 200px;
    overflow-y: auto;
  }
  .history_item{
    display: block;
    text-align: left;
    margin-bottom: 20px;
  }
  .item_top{
    display: flex;
    height: 20px;
    line-height: 20px;
  }
  .point{
    width: 5px;
    height: 5px;
    border-radius: 2.5px;
    background: #15bafe;
    position: relative;
    top: 8px;
    margin-right: 8px;
  }
  .h_name{
    margin-right: 10px;
  }
  .h_time{
    color: #15bafe;
    font-size: 12px;
  }
  .h_message{
    margin-left: 15px;
  }
  .reject_content{
    display: block;
    .reject_p{
      text-align: start;
      font-size: 12px;
      color: #404040;
    }
    .reject_p1{
      text-align: start;
      font-size: 12px;
      color: #404040;
      margin-top: 10px;
    }
    .reject_input{
      margin-top: 10px;
    }
    .btn_group{
      margin-top: 10px;
      .btns{
        width: 100%;
        margin-left: 0;
        margin-bottom: 10px;
      }
      .btns_s{
        width: 100%;
        margin-left: 0;
        margin-bottom: 10px;
        color: white;
        background: #409EFF;
      }
    }
  }
  .row_e{
    text-align: center;
  }
  .btn_delete{
    padding: 5px 20px;
    margin-left: 30px;
  }
  .btn_deletes{
    padding: 5px 20px;
  }
</style>
<style>
  .el-table th>.cell{
    text-align: center;
  }
  .el-dialog__body{
    padding: 0 20px;
  }
</style>



