<template>
    <div>
      <BreadCrumb class="bread" :breadData="breadData"></BreadCrumb>
      <div class="content">
        <p class="title">权限管理</p>
        <div class="conditions">
          <div>
            <el-select size="mini" class="select_normal" v-model="arrayValue" placeholder="排列">
              <el-option
                v-for="item in arrayOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
            <el-input
              class="select_normal"
              placeholder="搜索名称"
              size="mini"
              suffix-icon="el-icon-search"
              v-model="search_name">
            </el-input>
            <el-input
              class="select_normal"
              placeholder="搜索方法"
              size="mini"
              suffix-icon="el-icon-search"
              v-model="search_method">
            </el-input>
          </div>
          <el-button @click="addVisible = true" type="primary" size="mini" icon="el-icon-circle-plus-outline">新增权限</el-button>
        </div>
        <div class="tabs">
          <el-table
            :data="tableData"
            border
            :stripe="true"
            :header-row-style="{height:'40px'}"
            :header-cell-style="{padding:0,background:'#15bafe',color:'white'}"
            :row-style="{height:'40px'}"
            :cell-style="{padding:0}"
            style="width: 100%">
            <el-table-column
              prop="id"
              label="ID"
              width="50">
            </el-table-column>
            <el-table-column
              label="权限名称"
              min-width="200">
              <template slot-scope="scope">
                <span class="row_activity">{{scope.row.jurisdictionName}}</span>
              </template>
            </el-table-column>
            <el-table-column
              prop="description"
              label="介绍"
              width="200">
            </el-table-column>
            <el-table-column
              prop="type"
              label="类型(选择)"
              width="120">
            </el-table-column>
            <el-table-column
              prop="method"
              label="模块和方法"
              width="120">
            </el-table-column>
            <el-table-column
              prop="plate"
              label="板块"
              width="120">
            </el-table-column>
            <el-table-column
              prop="levelOne"
              label="一级菜单"
              width="120">
            </el-table-column>
            <el-table-column
              prop="levelTwo"
              label="二级菜单"
              width="120">
            </el-table-column>
            <el-table-column
              prop="status"
              label="状态"
              width="120">
            </el-table-column>
            <el-table-column
              prop="createTime"
              label="创建时间"
              width="120">
            </el-table-column>
            <el-table-column
              prop="updateTime"
              label="修改时间"
              width="120">
            </el-table-column>
            <el-table-column
              label="操作"
              width="240"
              fixed="right">
              <template slot-scope="scope">
                <el-button size="small" type="text">修改</el-button>
                <el-button size="small" type="text">删除</el-button>
                <el-button size="small" @click="menuVisible = true" type="text">菜单分配</el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
      <el-dialog :visible.sync="addVisible" width="400px">
        <div slot="title" class="dialog_head_title">
          <i class="iconfont icon-edit-square examine_icon"></i>
          <span>新增权限</span>
        </div>
        <div class="jurisdiction_content">
          <div class="jurisdiction_content_item">
            <label class="el_lbl">权限名称:</label>
            <el-input class="el_input" size="mini"></el-input>
          </div>
          <div class="jurisdiction_content_item">
            <label class="el_lbl">方法:</label>
            <el-input class="el_input" size="mini"></el-input>
          </div>
          <div class="jurisdiction_content_item">
            <label class="el_lbl">介绍:</label>
            <el-input placeholder="100字以内" class="el_input_text_area" type="textarea" :autosize="{ minRows: 3, maxRows: 3}"></el-input>
          </div>
          <div class="jurisdiction_content_item">
            <label class="el_lbl">类型选择:</label>
            <el-select size="mini" class="select_normal el_input" v-model="arrayValue" placeholder="操作权限">
              <el-option
                v-for="item in arrayOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
        </div>
        <div class="user_footer" slot="footer">
          <el-row class="row_e">
            <el-button class="btn_deletes" size="small" @click="menuVisible = false">取消</el-button>
            <el-button type="primary" size="small"  class="btn_delete">保存</el-button>
          </el-row>
        </div>
      </el-dialog>
      <el-dialog :visible.sync="menuVisible" width="1000px">
        <div slot="title" class="dialog_head_title">
          <i class="iconfont icon-menu examine_icon"></i>
          <span>菜单分配</span>
        </div>
        <div class="menu_content">
          <menuTrees @ee="getMenuItem"></menuTrees>
        </div>
        <div class="user_footer" slot="footer">
          <el-row class="row_e">
            <el-button class="btn_deletes" size="small" @click="remarkVisible = false">取消</el-button>
            <el-button type="primary" size="small" @click="handleSaveMenuJurisdiction"  class="btn_delete">保存</el-button>
          </el-row>
        </div>
      </el-dialog>
    </div>
</template>

<script>
  import BreadCrumb from '@/components/public/BreadCrumb';
  import menuTrees from '@/components/public/Jurisdiction';
    export default {
      name: "InsideJurisdiction",
      components: {
        BreadCrumb,
        menuTrees
      },
      data () {
        return {
          arrayValue: '',
          search_name: '',
          search_method: '',
          addVisible: false,
          menuVisible: false,
          menu_id: null,
          checkedJurisdiction: [],
          breadData: [{
            id: 1,
            name: '内部管理',
            urls: '/index',
            icon: 'icon-home'
          }, {
            id: 2,
            name: '权限管理',
            urls: '/index/test',
            icon: 'icon-home'
          }],
          arrayOptions: [{
            value: 1,
            label: '全部'
          }, {
            value: 2,
            label: '长期'
          }],
          tableData: [{
            id: 1,
            jurisdictionName: '摩托车视屏显示',
            description: '摩托车部门的视屏表格',
            type: '数据权限',
            method: 'Activity/uploading',
            plate: '网站内容管理',
            levelOne: '上传管理',
            levelTwo: '文章',
            status: 1,
            createTime: '2018/08/23',
            updateTime: '2018/09/26'
          }]
        }
      },
      methods: {
        /**
         * 子组件将勾选的菜单权限data传给当前页面
         * this.$store.state.menu.menu.id记录是哪一个菜单的id
         * @param data
         */
        getMenuItem:function (data) {
          console.log('data',data);
          this.menu_id = this.$store.state.menu.menu.id;
          this.checkedJurisdiction = data;
          /*console.log('219',this.$store.state.menu.menu)*/
        },
        handleSaveMenuJurisdiction: function () {
          console.log('保存：',this.menu_id ,this.checkedJurisdiction)
        }
      }
    }
</script>

<style scoped lang="less">
  .bread {
    margin: 10px;
  }
  .content {
    background: white;
    margin-left: 10px;
    margin-right: 10px;
    height: calc(100vh - 87px);
    width: calc(100vw - 221px);
    border-radius: 2px;
  }
  .title{
    text-align: left;
    padding: 10px;
    padding-left: 20px;
    font-size: 14px;
    border-bottom: 1px solid #f2f2f2;
  }
  .conditions{
    display: flex;
    margin-left: 10px;
    margin-top: 20px;
    justify-content: space-between;
    margin-right: 10px;
  }
  .select_normal{
    width: 150px;
    margin-right: 10px;
  }
  .tabs {
    margin: 20px 10px;
  }
  .dialog_head_title{
    text-align: start;
    font-size: 14px;
    border-bottom: 1px solid #f2f2f2;
    padding-bottom: 10px;
    .examine_icon{
      color: #15bafe;
    }
  }
.jurisdiction_content{
  .jurisdiction_content_item{
    display: flex;
    line-height: 28px;
    margin-left: 20px;
    margin-bottom: 10px;
    .el_lbl{
      width: 60px;
    }
    .el_input{
      width: 200px;
      margin-left: 20px;
    }
    .el_input_text_area{
      width: 200px;
      margin-left: 20px;
    }
  }
}
  .row_e{
    text-align: center;
    .btn_deletes{
      padding: 5px 15px;
      margin-right: 10px;
    }
    .btn_delete{
      padding: 5px 15px;
    }
  }
</style>
<style>
  .el-table th>.cell{
    text-align: center;
  }
  .el-dialog__body{
    padding: 0 20px !important;
  }
</style>



