export default {
  state: {
    menu: {}
  },
  getters: {
    showMenusInfo (state) {
      return state.menu
    }
  },
  actions: {
    getMenuInfo ({commit, state}, menu) {
      commit('getMenu', menu)
    }
  },
  mutations: {
    getMenu (state, menu) {
      if(menu===null){
        state.menu = {}
      }else{
        state.menu = menu
      }

    }
  }
}



