<template>
  <div>
    <BreadCrumb class="bread" :breadData="breadData"></BreadCrumb>
    <div class="content">
      <div class="menu_head">
        <el-button type="primary" size="mini">添加标题</el-button>
        <el-input size="mini" style="width: 200px"></el-input>
      </div>
      <div class="trees">
        <div class="tree_left">
          <menuTrees
            :children="treeData.children"
            :depth="0"
            :label="treeData.name"
            :id="treeData.id"
            @ee="getMenu"
          ></menuTrees>
        </div>
        <div class="tree_right">
          <div class="item_contain" :style="menuVisible?{display:'block'}:{display:'none'}">
            <div class="items">
              <label class="item_lb">菜单名称:</label>
              <el-input class="item_input" placeholder="请输入菜单名称" size="mini"></el-input>
            </div>
            <div class="items">
              <label class="item_lb">路径:</label>
              <el-input class="item_input" size="mini" placeholder="请输入路径 如:/index/activity"></el-input>
            </div>
            <div class="items">
              <label class="item_lb">图标:</label>
              <el-input class="item_input" size="mini" placeholder="请输入图标 如: icon-plus-square-fill"></el-input>
            </div>
            <el-row>
              <el-button size="mini" @click="menuVisible = false">取消</el-button>
              <el-button size="mini" type="primary">提交</el-button>
            </el-row>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import BreadCrumb from '@/components/public/BreadCrumb';

  export default {
    name: "SystemMenu",
    components: {
      BreadCrumb,
      menuTrees: {
        name: 'tree-menu',
        template: `
            <div class="tree-menu">
              <div class="label-wrapper">
                <div :style="indent" class="parent_menu">
                <div class="menu_label"  @click="toggleChildren">
                  <i v-if="children" class="iconfont icon_left" :class="showChildren?'icon-jian-xianxingfangkuang':'icon-jia-xianxingfangkuang'"></i>
                  {{ label }}
                </div>
                  <i class="iconfont icon-plus-square-fill icon_add" @click="openMenuModal(id)"></i>
                  <i class="iconfont icon-minus-square-fill icon_delete" @click="deleteMenu(id)"></i>
                  <i class="iconfont icon-edit-square icon_delete" @click="editMenu(id)"></i>
                </div>
              </div>
              <tree-menu
                v-if="showChildren"
                v-for="item in children"
                :children="item.children"
                :label="item.name"
                :depth="depth + 1"
                :key="item.id"
                :id="item.id"
                @ee="xx"
              >
              </tree-menu>
            </div>
          `,
        props: ['children', 'label', 'depth','id'],
        data() {
          return {
            showChildren: false,
            menu_visible: false,
            menu_id: null
          }
        },
        computed: {
          indent() {
            return {transform: `translate(${this.depth * 20}px)`}
          }
        },
        methods: {
          toggleChildren() {
            this.showChildren = !this.showChildren;
          },
          openMenuModal: function (id) {
            /*console.log(id)*/
            this.menu_visible = true;
            this.menu_id = id;
            this.$emit('ee',{id:this.menu_id,menu_visible:this.menu_visible});
            this.$store.dispatch('getMenuInfo',{id:this.menu_id,menu_visible:this.menu_visible}).then(res=>{
              console.log(this.$store.state.menu.menu)
            });

            /*console.log(111,this.menu_visible,this.menu_id )*/
          },
          deleteMenu: function (id) {
            console.log(id)
          },
          editMenu: function (id) {
            console.log(id)
          },
          xx: function () {
            /*console.log(111,this.menu_id,this.menu_visible)*/
            this.$emit('ee',{id:this.menu_id,menu_visible:this.menu_visible});
          }
        }
      }
    },
    data() {
      return {
        show: false,
        typeTitle: '',
        index: 0,
        menuVisible: false,
        menu_id: null,
        breadData: [{
          id: 1,
          name: '系统管理',
          urls: '/index',
          icon: 'icon-home'
        }, {
          id: 2,
          name: '菜单管理',
          urls: '/index/test',
          icon: 'icon-home'
        }],
        treeData: {
          id: 0,
          name: 'root',
          children: [{
            id: 1,
            name: '菜单1',
            children: [{
              id: 11,
              name: '菜单11'
            }, {
              id: 12,
              name: '菜单12'
            }]
          }, {
            id: 2,
            name: '菜单2',
            children: [{
              id: 21,
              name: '菜单21'
            }, {
              id: 22,
              name: '菜单22'
            }]
          }]
        }
      }
    },
    methods:{
      getMenu: function (data) {
        setTimeout(()=>{
          let childrenData = this.$store.state.menu.menu;
          this.menuVisible = childrenData.menu_visible;
          this.menu_id = childrenData.id;
          console.log(childrenData,this.menu_id,this.menuVisible)
        })

      }
    }
  }
</script>

<style scoped lang="less">
  .bread {
    margin: 10px;
  }
.menu_head{
  text-align: -webkit-left;
  margin-bottom: 10px;
  padding-top: 20px;
  margin-left: 10px;
}
  .content {
    background: white;
    margin-left: 10px;
    margin-right: 10px;
    height: calc(100vh - 87px);
    width: calc(100vw - 221px);
    border-radius: 2px;
  }

  .trees {
    display: flex;
    margin-left: 10px;
    .tree_left{
      width: 50%;
    }
    .tree_right{
      width: 50%;
      .item_contain{
        border: 1px solid #f2f2f2;
        padding: 20px;
        max-width: 310px;
        .items{
          display: flex;
          font-size: 13px;
          height: 28px;
          line-height: 28px;
          margin-bottom: 20px;
          .item_lb{
            width: 80px;
            text-align: left;
          }
          .item_input{
            width: 200px;
          }
        }
      }
    }
  }

  .tree-menu {
    .label-wrapper {
      padding-bottom: 10px;
      margin-bottom: 10px;
      border-bottom: 1px solid #ccc;
      .has-children {
        cursor: pointer;
      }
    }
  }
</style>
<style>
  .parent_menu{
    display: flex;
  }
  .menu_label{
    cursor: pointer;
    margin-right: 10px;
  }
  .icon_add{
    color: #15bafe;
    position: relative;
    top: 1px;
  }
  .icon_delete{
    color: #15bafe;
    position: relative;
    top: 1px;
    margin-left: 10px;
  }
  .icon_left{
    color: #009afe;
  }
</style>



