<template>
  <div>
    <BreadCrumb class="bread" :breadData="breadData"></BreadCrumb>
    <div class="content">
      <div class="trees">
        <div class="tree_left">
          <div class="left_content">
            <menuTrees
              :children="treeData.children"
              :depth="0"
              :label="treeData.name"
              :id="treeData.id"
              @ee="getMenu"
            ></menuTrees>
          </div>

        </div>
        <div class="tree_right">
          <div class="item_contain" :style="menuVisible?{display:'block'}:{display:'none'}">
            <div class="jurisdiction_content">
              <el-checkbox class="all_check" :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
              <div style="margin: 15px 0;"></div>
              <el-checkbox-group class="check_group" v-model="checkedJurisdiction" @change="handleCheckedCitiesChange">
                <el-checkbox class="check_item" v-for="item in jurisdictionData" :label="item" :key="item.id">
                  <span class="check_lab">{{item.name}}<span class="line"></span>{{item.method}}</span>
                </el-checkbox>
              </el-checkbox-group>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import BreadCrumb from '@/components/public/BreadCrumb';
  const jurisdictions = [{
    id: 1,
    name: '基本信息',
    description: '电视剧的方式开始看',
    method: 'system/basic',
    type: 1
  },{
    id: 2,
    name: '删除',
    description: '电视剧的方式开始看',
    method: 'system/basic',
    type: 1
  },{
    id: 3,
    name: '修改',
    description: '电视剧的方式开始看',
    method: 'system/basic',
    type: 1
  },{
    id: 4,
    name: '添加备注',
    description: '电视剧的方式开始看',
    method: 'system/basic',
    type: 1
  },{
    id: 5,
    name: '驳回',
    description: '电视剧的方式开始看',
    method: 'system/basic',
    type: 1
  },{
    id: 6,
    name: '基本信息',
    description: '电视剧的方式开始看',
    method: 'system/basic',
    type: 1
  },{
    id: 7,
    name: '删除',
    description: '电视剧的方式开始看',
    method: 'system/basic',
    type: 1
  },{
    id: 8,
    name: '修改',
    description: '电视剧的方式开始看',
    method: 'system/basic',
    type: 1
  },{
    id: 9,
    name: '添加备注',
    description: '电视剧的方式开始看',
    method: 'system/basic',
    type: 1
  },{
    id: 10,
    name: '驳回',
    description: '电视剧的方式开始看',
    method: 'system/basic',
    type: 1
  }];
  export default {
    name: "SystemMenuJurisdiction",
    components: {
      BreadCrumb,
      menuTrees: {
        name: 'tree-menu',
        template: `
            <div class="tree-menu">
              <div class="label-wrapper">
                <div :style="indent" class="parent_menu">
                <div class="menu_label"  @click="toggleChildren">
                  <i v-if="children" class="iconfont icon_left" :class="showChildren?'icon-jian-xianxingfangkuang':'icon-jia-xianxingfangkuang'"></i>
                  {{ label }}
                </div>
                  <!--<i class="iconfont icon-plus-square-fill icon_add" @click="openMenuModal(id)"></i>-->
                  <!--<i class="iconfont icon-minus-square-fill icon_delete" @click="deleteMenu(id)"></i>
                  <i class="iconfont icon-edit-square icon_delete" @click="editMenu(id)"></i>-->
                </div>
              </div>
              <tree-menu
                v-if="showChildren"
                v-for="item in children"
                :children="item.children"
                :label="item.name"
                :depth="depth + 1"
                :key="item.id"
                :id="item.id"
                @ee="xx"
              >
              </tree-menu>
              <el-dialog :visible.sync="addVisible" width="400px">
                <div slot="title" class="dialog_head_title">
                  <i class="iconfont icon-edit-square examine_icon"></i>
                  <span>新增权限</span>
                </div>
                <div class="jurisdiction_content">
                  <div class="jurisdiction_content_item">
                    <label class="el_lbl">权限名称:</label>
                    <el-input class="el_input" size="mini"></el-input>
                  </div>
                  <div class="jurisdiction_content_item">
                    <label class="el_lbl">方法:</label>
                    <el-input class="el_input" size="mini"></el-input>
                  </div>
                  <div class="jurisdiction_content_item">
                    <label class="el_lbl">介绍:</label>
                    <el-input placeholder="100字以内" class="el_input_text_area" type="textarea" :autosize="{ minRows: 3, maxRows: 3}"></el-input>
                  </div>
                  <div class="jurisdiction_content_item">
                    <label class="el_lbl">类型选择:</label>
                    <el-select size="mini" class="select_normal el_input" v-model="arrayValue" placeholder="操作权限">
                      <el-option
                        v-for="item in arrayOptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                      </el-option>
                    </el-select>
                  </div>
                </div>
                <div class="user_footer" slot="footer">
                  <el-row class="row_e">
                    <el-button class="btn_deletes" size="small" @click="menuVisible = false">取消</el-button>
                    <el-button type="primary" size="small"  class="btn_delete">保存</el-button>
                  </el-row>
                </div>
              </el-dialog>
            </div>
          `,
        props: ['children', 'label', 'depth','id'],
        data() {
          return {
            showChildren: false,
            menu_visible: false,
            menu_id: null,
            addVisible: false,
            arrayValue: '',
            arrayOptions: [{
              value: 1,
              label: '全部'
            }, {
              value: 2,
              label: '长期'
            }],
          }
        },
        computed: {
          indent() {
            return {transform: `translate(${this.depth * 20}px)`}
          }
        },
        methods: {
          toggleChildren() {
            this.showChildren = !this.showChildren;
          },
          openMenuModal: function (id) {
            /*console.log(id)*/
            this.menu_visible = true;
            this.addVisible = true;
            this.menu_id = id;
            this.$emit('ee',{id:this.menu_id,menu_visible:this.menu_visible});
            this.$store.dispatch('getMenuInfo',{id:this.menu_id,menu_visible:this.menu_visible}).then(res=>{
              console.log(this.$store.state.menu.menu)
            });

            /*console.log(111,this.menu_visible,this.menu_id )*/
          },
          deleteMenu: function (id) {
            console.log(id)
          },
          editMenu: function (id) {
            console.log(id)
          },
          xx: function () {
            /*console.log(111,this.menu_id,this.menu_visible)*/
            this.$emit('ee',{id:this.menu_id,menu_visible:this.menu_visible});
          }
        }
      }
    },
    data() {
      return {
        show: false,
        typeTitle: '',
        index: 0,
        menuVisible: true,
        menu_id: null,
        breadData: [{
          id: 1,
          name: '系统管理',
          urls: '/index',
          icon: 'icon-home'
        }, {
          id: 2,
          name: '菜单权限',
          urls: '/index/test',
          icon: 'icon-home'
        }],
        treeData: {
          id: 0,
          name: 'root',
          children: [{
            id: 1,
            name: '菜单1',
            children: [{
              id: 11,
              name: '菜单11'
            }, {
              id: 12,
              name: '菜单12'
            }]
          }, {
            id: 2,
            name: '菜单2',
            children: [{
              id: 21,
              name: '菜单21'
            }, {
              id: 22,
              name: '菜单22'
            }]
          }],
        },
        checkAll: false,
        checkedJurisdiction: [],
        jurisdictionData: jurisdictions,
        isIndeterminate: true
      }
    },
    methods:{
      getMenu: function (data) {
        setTimeout(()=>{
          let childrenData = this.$store.state.menu.menu;
          this.menuVisible = childrenData.menu_visible;
          this.menu_id = childrenData.id;
          console.log(childrenData,this.menu_id,this.menuVisible)
        })
      },
      handleCheckAllChange(val) {
        console.log(val)
        this.checkedJurisdiction = val ? jurisdictions : [];
        console.log(this.checkedJurisdiction)
        this.isIndeterminate = false;
      },
      handleCheckedCitiesChange(value) {
        console.log(value)
        let checkedCount = value.length;
        this.checkAll = checkedCount === this.jurisdictionData.length;
        this.isIndeterminate = checkedCount > 0 && checkedCount < this.jurisdictionData.length;
      }
    }
  }
</script>

<style scoped lang="less">
  .bread {
    margin: 10px;
  }
  .menu_head{
    text-align: -webkit-left;
    margin-bottom: 10px;
  }
  .content {
    background: white;
    margin-left: 10px;
    margin-right: 10px;
    height: calc(100vh - 87px);
    width: calc(100vw - 221px);
    border-radius: 2px;
  }

  .trees {
    display: flex;
    .tree_left{
      width: 50%;
      .left_content{
        border: 1px solid #f2f2f2;
        margin: 20px 10px;
        padding: 10px;
        min-height: 395px;
      }
    }
    .tree_right{
      width: 50%;
      .item_contain{
        border: 1px solid #f2f2f2;
        padding: 20px;
        max-width: 310px;
        margin-top: 20px;
        .items{
          display: flex;
          font-size: 13px;
          height: 28px;
          line-height: 28px;
          margin-bottom: 20px;
          .item_lb{
            width: 80px;
            text-align: left;
          }
          .item_input{
            width: 200px;
          }
        }
      }
    }
  }

  .tree-menu {
    .label-wrapper {
      padding-bottom: 10px;
      margin-bottom: 10px;
      border-bottom: 1px solid #ccc;
      .has-children {
        cursor: pointer;
      }
    }
  }
  .jurisdiction_content{
    max-height: 400px;
    overflow-y: auto;
    .all_check{
      width: 100%;
      text-align: -webkit-left;
    }
    .check_group{
      .check_item{
        display: -webkit-box;
        margin-bottom: 15px;
        .check_lab{
          background: #409EFF;
          color: white;
          padding: 5px 10px;
          border-radius: 2px;
          .line{
            height: 10px;
            border-left: 1px solid #ffffff;
            margin: 0 10px;
          }
        }
      }
    }
  }
</style>
<style lang="less">
  .parent_menu{
    display: flex;
  }
  .menu_label{
    cursor: pointer;
    margin-right: 10px;
  }
  .icon_add{
    color: #15bafe;
    position: relative;
    top: 1px;
  }
  .icon_delete{
    color: #15bafe;
    position: relative;
    top: 1px;
    margin-left: 10px;
  }
  .icon_left{
    color: #009afe;
  }
  .el-checkbox+.el-checkbox{
    margin-left: 0 !important;
  }
  .dialog_head_title{
    text-align: start;
    font-size: 14px;
    border-bottom: 1px solid #f2f2f2;
    padding-bottom: 10px;
  .examine_icon{
    color: #15bafe;
  }
  }
  .jurisdiction_content{
  .jurisdiction_content_item{
    display: flex;
    line-height: 28px;
    margin-left: 20px;
    margin-bottom: 10px;
  .el_lbl{
    width: 60px;
  }
  .el_input{
    width: 200px;
    margin-left: 20px;
  }
  .el_input_text_area{
    width: 200px;
    margin-left: 20px;
  }
  }
  }
</style>



