<template>
  <div>
    <BreadCrumb class="bread" :breadData="breadData"></BreadCrumb>
    <div class="content">
      <p class="title">角色管理</p>
      <div class="conditions">
        <div class="condition">
          <p class="role_total">当前共有250个角色</p>
          <el-input
            class="select_normal"
            placeholder="姓名搜索"
            size="mini"
            suffix-icon="el-icon-search"
            v-model="search_name">
          </el-input>
          <el-select size="mini" class="select_normal" v-model="arrayValue" placeholder="角色状态">
            <el-option
              v-for="item in arrayOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </div>
        <el-button @click="showUserDialog(1)" type="primary" size="mini" icon="el-icon-circle-plus-outline">新增用户</el-button>
      </div>
      <div class="tabs">
        <el-table
          :data="tableData"
          border
          :stripe="true"
          :header-row-style="{height:'40px'}"
          :header-cell-style="{padding:0,background:'#15bafe',color:'white'}"
          :row-style="{height:'40px'}"
          :cell-style="{padding:0}"
          style="width: 100%">
          <el-table-column
            prop="id"
            label="用户ID"
            width="100">
          </el-table-column>
          <el-table-column
            label="姓名"
            min-width="120">
            <template slot-scope="scope">
              <span class="row_activity">{{scope.row.userName}}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="userNo"
            label="工号">
          </el-table-column>
          <el-table-column
            prop="department"
            label="所属部门(选择)"
            width="120">
          </el-table-column>
          <el-table-column
            prop="job"
            label="职位"
            width="120">
          </el-table-column>
          <el-table-column
            label="证件照"
            width="120">
            <template slot-scope="scope">
              <img :src="scope.row.idPhoto" class="id_photo"/>
            </template>
          </el-table-column>
          <el-table-column
            prop="role"
            label="角色"
            width="120">
          </el-table-column>
          <el-table-column
            prop="status"
            label="状态(选择)"
            width="120">
          </el-table-column>
          <el-table-column
            prop="createTime"
            label="创建时间"
            width="120">
          </el-table-column>
          <el-table-column
            label="操作"
            width="240"
            fixed="right">
            <template slot-scope="scope">
              <el-button size="small" type="text" @click="showUserDialog(2)">修改</el-button>
              <el-button size="small" type="text">删除</el-button>
              <el-button size="small" type="text" @click="handleJumpToJurisdiction(1)">角色分配</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <el-dialog :visible.sync="userVisible" width="400px">
      <div slot="title" class="dialog_head_title">
        <i class="iconfont icon-account examine_icon"></i>
        <span>新增用户</span>
      </div>
      <div class="jurisdiction_content">
        <div class="jurisdiction_content_item">
          <label class="el_lbl">姓名:</label>
          <el-input class="el_input" size="mini"></el-input>
        </div>
        <div class="jurisdiction_content_item">
          <label class="el_lbl">部门选择:</label>
          <el-select size="mini" class="el_input" v-model="arrayValue" placeholder="角色状态">
            <el-option
              v-for="item in arrayOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </div>
        <div class="jurisdiction_content_item">
          <label class="el_lbl">职位:</label>
          <el-select size="mini" class="el_input" v-model="arrayValue" placeholder="角色状态">
            <el-option
              v-for="item in arrayOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </div>
        <div class="jurisdiction_content_item">
          <label class="el_lbl">证件照:</label>
          <el-upload
            class="avatar-uploader up_img"
            action="https://jsonplaceholder.typicode.com/posts/"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload">
            <img v-if="imageUrl" :src="imageUrl" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </div>
      </div>
      <div class="user_footer" slot="footer">
        <el-row class="row_e">
          <el-button class="btn_deletes" size="small" @click="userVisible = false">取消</el-button>
          <el-button type="primary" size="small"  class="btn_delete">保存</el-button>
        </el-row>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import BreadCrumb from '@/components/public/BreadCrumb';
  export default {
    name: "InsideUser",
    components: {
      BreadCrumb
    },
    data() {
        return {
          userVisible: false,
          dialogImageUrl: '',
          search_name: '',
          arrayValue: '',
          arrayOptions: [],
          imageUrl: '',
          breadData: [{
            id: 1,
            name: '内部管理',
            urls: '/index',
            icon: 'icon-home'
          }, {
            id: 2,
            name: '用户管理',
            urls: '/index/test',
            icon: 'icon-home'
          }],
          tableData: [{
            id: 1,
            userName: '大可可',
            userNo: 9587,
            department: '网站管理设计部',
            job: '网站管理职员',
            idPhoto: 'http://cdn.kushualab.com/0E2B038E-FD7D-4883-955C-4D900B6A7A25.png',
            role: '上传管理员',
            status: 1,
            createTime: '2018-09-26'
          }]
        }
    },
    methods: {
      handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isJPG && isLt2M;
      },
      showUserDialog: function (index) {
        console.log(index);
        this.userVisible = true;
      },
      handleJumpToJurisdiction: function (id) {
        this.$store.dispatch('getTabsInfo',{icon:'icon-home',id:30021,title:'用户角色分配',urls:'/index/inside/user-jurisdiction'});
        this.$router.push({
          path: '/index/inside/user-jurisdiction',
          params: {
            id: id
          }
        })
      }
    }
  }
</script>

<style scoped lang="less">
  .bread {
    margin: 10px;
  }
  .content {
    background: white;
    margin-left: 10px;
    margin-right: 10px;
    height: calc(100vh - 87px);
    width: calc(100vw - 221px);
    border-radius: 2px;
  }
  .title{
    text-align: left;
    padding: 10px;
    padding-left: 20px;
    font-size: 14px;
    border-bottom: 1px solid #f2f2f2;
  }
  .conditions{
    display: flex;
    margin-left: 10px;
    margin-top: 20px;
    justify-content: space-between;
    margin-right: 10px;
  .condition{
    display: flex;
  }
  }
  .role_total{
    background: #409EFF;
    line-height: 28px;
    color: white;
    font-size: 12px;
    margin-right: 20px;
    padding: 0 15px;
  }
  .select_normal{
    width: 150px;
    margin-right: 10px;
  }
  .tabs {
    margin: 20px 10px;
  }
  .dialog_head_title{
    text-align: start;
    font-size: 14px;
    border-bottom: 1px solid #f2f2f2;
    padding-bottom: 10px;
  .examine_icon{
    color: #15bafe;
  }
  }
  .id_photo{
    width: 40px;
    height: 40px;
    padding-top: 8px;
  }
  .jurisdiction_content{
    .jurisdiction_content_item{
      display: flex;
      line-height: 28px;
      margin-left: 20px;
      margin-bottom: 10px;
      .el_lbl{
        width: 60px;
        text-align: left;
      }
      .el_input{
        width: 200px;
        margin-left: 20px;
      }
      .el_input_text_area{
        width: 200px;
        margin-left: 20px;
      }
    }
  }
  .up_img{
    margin-left: 20px;
  }
</style>

<style>
  .el-table th>.cell{
    text-align: center;
  }
  .el-dialog__body{
    padding: 0 20px !important;
  }
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 200px;
    height: 178px;
    line-height: 178px !important;
    text-align: center;
  }
  .avatar {
    width: 200px;
    height: 178px;
    display: block;
  }
</style>



