<template>
    <div>
      <BreadCrumb class="bread" :breadData="breadData"></BreadCrumb>
      <div class="content">
        <p class="title">新活动申请</p>
        <div class="content_contain">
          <div class="conditions">
            <el-select size="mini" class="select_normal" @change="handleTypeChange" v-model="typeValue" placeholder="类型">
              <el-option
                v-for="item in typeOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
            <el-select size="mini" class="select_normal" @change="handleTypeChange" v-model="plateValue" placeholder="板块">
              <el-option
                v-for="item in plateOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
            <el-select size="mini" class="select_normal" @change="handleTypeChange" v-model="levelValue" placeholder="类型">
              <el-option
                v-for="item in levelOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
            <el-select size="mini" class="select_normal" @change="handleTypeChange" v-model="statusValue" placeholder="类型">
              <el-option
                v-for="item in statusOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
          <div class="tables">
            <el-table
              :data="tableData"
              :header-row-style="{height:'40px'}"
              :header-cell-style="{padding:0,background:'#15bafe',color:'white'}"
              :row-style="{height:'40px'}"
              :cell-style="{padding:0}"
              border
              :stripe="true"
              style="width: 100%">
              <el-table-column
                prop="id"
                label="ID"
                width="50">
              </el-table-column>
              <el-table-column
                label="活动名称"
                min-width="200">
                <template slot-scope="scope">
                  <span class="row_activity">{{scope.row.activityName}}</span>
                </template>
              </el-table-column>
              <el-table-column
                prop="type"
                label="类型"
                width="80">
              </el-table-column>
              <el-table-column
                prop="startTime"
                label="开始时间"
                width="120">
              </el-table-column>
              <el-table-column
                prop="endTime"
                label="报名截止时间"
                width="120">
              </el-table-column>
              <el-table-column
                prop="plate"
                label="板块"
                width="80">
              </el-table-column>
              <el-table-column
                prop="level"
                label="二级"
                width="80">
              </el-table-column>
              <el-table-column
                prop="address"
                label="活动地址"
                width="120">
              </el-table-column>
              <el-table-column
                prop="contacts"
                label="联系人"
                width="80">
              </el-table-column>
              <el-table-column
                prop="phone"
                label="电话"
                width="120">
              </el-table-column>
              <el-table-column
                label="状态"
                width="100">
                <template slot-scope="scope">
                  {{scope.row.status}}
                </template>
              </el-table-column>
              <el-table-column
                prop="applyTime"
                label="申请时间"
                width="120">
              </el-table-column>
              <el-table-column
                label="操作"
                width="240"
                fixed="right">
                <template slot-scope="scope">
                  <el-button size="small" type="text">基本信息</el-button>
                  <el-button size="small" type="text">查看反馈</el-button>
                  <el-button size="small" type="text">预览活动</el-button>
                  <el-button size="small" @click="examineVisible = true" type="text">提交审核</el-button>
                  <el-button size="small" type="text" @click="deleteVisible = true">删除</el-button>
                  <el-button size="small" type="text" @click="remarkVisible = true">添加备注</el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </div>
      </div>
      <el-dialog :visible.sync="examineVisible" width="400px">
        <div slot="title" class="dialog_head_title">
          <i class="iconfont icon-geren examine_icon"></i>
          <span>提交审核</span>
        </div>
        <div class="examine_content">
          <el-upload
            class="up_safe"
            action="https://jsonplaceholder.typicode.com/posts/"
            :on-preview="handlePreview"
            :on-remove="handleRemove"
            :before-remove="beforeRemove"
            multiple
          >
            <div class="up_safe_header">
              <el-button size="small" class="btn_up" type="primary">点击上传</el-button>
              <p>上传格式:doc || docx</p>
            </div>

          </el-upload>
          <div class="up_img">
            <p class="remark">上传相关图片，格式:png,jpge,gif</p>
            <el-upload
              class="up_img_item"
              action="https://jsonplaceholder.typicode.com/posts/"
              list-type="picture-card"
              :on-preview="handlePictureCardPreview"
              :on-remove="handleRemove"
              multiple
            >
              <i class="el-icon-plus icon_plus"></i>
            </el-upload>
          </div>
        </div>
        <div class="user_footer" slot="footer">
          <el-row>
            <el-button class="btn_foot" @click="examineVisible = false">取消</el-button>
            <el-button type="primary" class="btn_foot" @click="handleExamine">提交</el-button>
          </el-row>
        </div>
      </el-dialog>
      <el-dialog :visible.sync="deleteVisible" width="300px">
        <div slot="title" class="dialog_delete_head_title">
          <i class="iconfont icon-trash delete_icon"></i>
          <span>删除</span>
        </div>
        <div class="delete_content">
          <img class="boolean_delete" :src="warmImg"/>
          <p>确定删除吗？</p>
        </div>
        <div class="user_footer" slot="footer">
          <el-row class="row_e">
            <el-button class="btn_deletes" size="small" @click="deleteVisible = false">取消</el-button>
            <el-button type="primary" size="small"  class="btn_delete" @click="handleDelete">提交</el-button>
          </el-row>
        </div>
      </el-dialog>
      <el-dialog :visible.sync="remarkVisible" width="400px">
        <div slot="title" class="dialog_delete_head_title">
          <i class="iconfont icon-edit-square delete_icon"></i>
          <span>添加备注</span>
        </div>
        <div class="remark_content">
          <p class="remark_p">内容</p>
          <el-input
            type="textarea"
            :rows="2"
            class="text_area"
            placeholder="商家的修改，取消，结束活动都将显示在历史里面"
            v-model="remark">
          </el-input>
          <p class="history_p">历史</p>
          <div class="h_content">
            <div class="history_item" v-for="(item,index) in historyData" :key="index">
              <div class="item_top">
                <div class="point"/>
                <p class="h_name">{{item.name}}</p>
                <p class="h_time">{{item.createTime}}</p>
              </div>
              <p class="h_message">{{item.message}}</p>
            </div>
          </div>
        </div>
        <div class="user_footer" slot="footer">
          <el-row class="row_e">
            <el-button class="btn_deletes" size="small" @click="remarkVisible = false">取消</el-button>
            <el-button type="primary" size="small"  class="btn_delete" @click="handleDelete">提交</el-button>
          </el-row>
        </div>
      </el-dialog>
    </div>
</template>

<script>
    import BreadCrumb from "../public/BreadCrumb";
    export default {
      name: "ActivityApply",
      components: {BreadCrumb},
      data () {
        return {
          examineVisible: false,
          deleteVisible: false,
          remarkVisible: false,
          remark: '',
          warmImg: require("../../assets/image/warm.png"),
          breadData: [{
            id: 1,
            name: '活动',
            urls: '/index',
            icon: 'icon-home'
          },{
            id: 2,
            name: '新活动申请',
            urls: '/index/test',
            icon: 'icon-home'
          }],
          typeOptions: [{
            value: 1,
            label: '全部'
          }, {
            value: 2,
            label: '长期'
          }],
          plateOptions: [{
            value: 1,
            label: '全部'
          }, {
            value: 2,
            label: '汽车'
          }],
          levelOptions: [{
            value: 1,
            label: '全部'
          }, {
            value: 2,
            label: '改装'
          }],
          statusOptions: [{
            value: 1,
            label: '全部'
          }, {
            value: 2,
            label: '被驳回'
          }, {
            value: 3,
            label: '待审核'
          }, {
            value: 4,
            label: '待提交'
          }, {
            value: 5,
            label: '已通过'
          }],
          typeValue: null,
          plateValue: null,
          levelValue: null,
          statusValue: null,
          dialogImageUrl: '',
          dialogVisible: false,
          tableData:[{
            id: 1,
            activityName: '如何给汽车加油如何给汽车加油如何给汽车加油',
            type: 1,
            startTime: '2018-07-11',
            endTime: '2018-12-01',
            plate: 2,
            level: 3,
            address: '重庆渝北',
            contacts: '张三',
            phone: '13595555555',
            status: 2,
            applyTime: '2018-03-01'
          },{
            id: 2,
            activityName: '如何给汽车加油如何给汽车加油如何给汽车加油',
            type: 1,
            startTime: '2018-07-11',
            endTime: '2018-12-01',
            plate: 2,
            level: 3,
            address: '重庆渝北',
            contacts: '张三',
            phone: '13595555555',
            status: 2,
            applyTime: '2018-03-01'
          },{
            id: 3,
            activityName: '如何给汽车加油如何给汽车加油如何给汽车加油',
            type: 1,
            startTime: '2018-07-11',
            endTime: '2018-12-01',
            plate: 2,
            level: 3,
            address: '重庆渝北',
            contacts: '张三',
            phone: '13595555555',
            status: 2,
            applyTime: '2018-03-01'
          }],
          historyData: [{
            id: 1,
            name: '康宇',
            createTime: '2018/08/07',
            message: '联系了对方愿意长期合作'
          },{
            id: 2,
            name: '康宇',
            createTime: '2018/08/07',
            message: '联系了对方愿意长期合作联系了对方愿意长期合作联系了对方愿意长期合作联系了对方愿意长期合作'
          },{
            id: 3,
            name: '康宇',
            createTime: '2018/08/07',
            message: '联系了对方愿意长期合作'
          },{
            id: 4,
            name: '康宇',
            createTime: '2018/08/07',
            message: '联系了对方愿意长期合作'
          }]
        }
      },
      methods: {
        handleTypeChange: function (e) {
          console.log(e,this.typeValue)
        },
        handleRemove(file, fileList) {
          console.log(file, fileList);
        },
        handlePreview(file) {
          console.log(file);
        },
        handleExceed(files, fileList) {
          this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
        },
        beforeRemove(file, fileList) {
          return this.$confirm(`确定移除 ${ file.name }？`);
        },
        handlePictureCardPreview(file) {
          this.dialogImageUrl = file.url;
          this.dialogVisible = true;
        },
        handleExamine: function () {
          this.examineVisible = false;
        },
        handleDelete: function () {
          this.deleteVisible = false;
        }
      }
    }
</script>

<style scoped>
  .bread{
    margin: 10px;
  }
  .content{
    background: white;
    margin-left: 10px;
    margin-right: 10px;
    height: calc(100vh - 87px);
    width: calc(100vw - 221px);
    border-radius: 2px;
  }
  .title{
    text-align: left;
    padding: 10px;
    padding-left: 20px;
    font-size: 14px;
    border-bottom: 1px solid #f2f2f2;
  }
  .conditions{
    display: flex;
    margin-left: 10px;
    margin-top: 20px;
  }
  .select_normal{
    width: 100px;
    margin-right: 10px;
  }
  .tables{
    margin: 20px 10px;
  }
  .row_activity{
    width: 200px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .dialog_head_title{
    height: 16px;
    border-bottom: 1px solid #cccccc;
    padding-bottom: 10px;
    text-align: left;
    font-size: 15px;
  }
  .dialog_delete_head_title{
    height: 16px;
    border-bottom: 1px solid #cccccc;
    padding-bottom: 10px;
    text-align: left;
    font-size: 15px;
  }
  .delete_icon{
    color: #15bafe;
    font-size: 18px;
    top: 1px;
    position: relative;
  }
  .examine_icon{
    color: #15bafe;
  }
  .up_safe{
    height: 120px;
    border: 1px solid #f2f2f2;
    overflow-y: auto;
  }
  .up_safe_header{
    display: flex;
    line-height: 24px;
    margin-top: 10px;
    position: relative;
    left: -60px;
  }
  .btn_up{
    padding: 5px 10px;
    margin-right: 20px;
  }
  .up_img{
    height: 130px;
    border: 1px solid #f2f2f2;
    margin-top: 10px;
    display: grid;
  }
  .remark{
    text-align: left;
    margin-left: 10px;
  }
  .up_img_header{
    display: flex;
    line-height: 24px;
    margin-top: 10px;
    position: absolute;
    left: 30px;
    width: 250px;
  }
  .up_img_item{
    /*width: 40px;
    height: 40px;*/
    display: -webkit-box;
    overflow-y: auto;
    height: 100px;
    margin-left: 10px;
    margin-top: 10px;
    margin-right: 10px;
    flex-wrap: wrap;
  }
  .icon_plus{
    position: relative;
    bottom: 30px;
  }
  .delete_content{

  }
  .boolean_delete{
    height: 30px;
    width: 30px;
  }
  .delete_content p{
    margin-top: 5px;
  }
  .row_e{
    text-align: center;
  }
  .btn_delete{
    padding: 5px 15px;
    margin-left: 30px;
  }
  .btn_deletes{
    padding: 5px 15px;
  }
  .remark_content{
    position: relative;
    top: -10px;
  }
  .remark_p{
    text-align: left;
  }
  .text_area{
    margin-top: 8px;
  }
  .history_p{
    text-align: left;
    margin-top: 8px;
    border-bottom: 1px solid #f2f2f2;
    padding-bottom: 10px;
  }
  .h_content{
    margin-top: 10px;
    height: 200px;
    overflow-y: auto;
  }
  .history_item{
    display: block;
    text-align: left;
    margin-bottom: 20px;
  }
  .item_top{
    display: flex;
    height: 20px;
    line-height: 20px;
  }
  .point{
    width: 5px;
    height: 5px;
    border-radius: 2.5px;
    background: #15bafe;
    position: relative;
    top: 8px;
    margin-right: 8px;
  }
  .h_name{
    margin-right: 10px;
  }
  .h_time{
    color: #15bafe;
    font-size: 12px;
  }
  .h_message{
    margin-left: 15px;
  }
</style>
<style lang="less">
  .el-table th>.cell{
    text-align: center;
  }
  .el-upload-list{
    height: 85px;
    /*overflow: auto;*/
    width:300px;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .el-upload--picture-card{
    width: 90px;
    height: 90px;
  }
  /*.el-upload--picture-card i{
    position: relative;
    bottom: 30px;
  }*/
  .el-upload-list--picture-card .el-upload-list__item{
    width: 90px;
    height: 90px;
  }
  .el-dialog__body{
    padding-top: 15px;
    padding-bottom: 15px;
  }
  .up_safe{
    .el-upload-list__item{
      text-align: left !important;
    }
  }
</style>



