<template>
  <div>
    <BreadCrumb class="bread" :breadData="breadData"></BreadCrumb>
    <div class="content">
      <p class="title">部门管理</p>
      <div class="conditions">
        <div class="condition">
        </div>
        <el-button @click="editVisible = true" type="primary" size="mini" icon="el-icon-circle-plus-outline">新增部门
        </el-button>
      </div>
      <div class="tabs">
        <el-table
          :data="tableData"
          border
          :stripe="true"
          :header-row-style="{height:'40px'}"
          :header-cell-style="{padding:0,background:'#15bafe',color:'white'}"
          :row-style="{height:'40px'}"
          :cell-style="{padding:0}"
          style="width: 100%">
          <el-table-column
            prop="id"
            label="部门ID"
            width="100">
          </el-table-column>
          <el-table-column
            label="名称"
            min-width="200">
            <template slot-scope="scope">
              <span class="row_activity">{{scope.row.departmentName}}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="description"
            label="介绍">
          </el-table-column>
          <el-table-column
            prop="createTime"
            label="创建时间"
            width="120">
          </el-table-column>
          <el-table-column
            label="操作"
            width="240"
            fixed="right">
            <template slot-scope="scope">
              <el-button size="small" type="text" @click="editVisible = true">修改</el-button>
              <el-button size="small" type="text">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <el-dialog :visible.sync="editVisible" width="400px">
      <div slot="title" class="dialog_head_title">
        <i class="iconfont icon-edit-square examine_icon"></i>
        <span>创建部门</span>
      </div>
      <div class="jurisdiction_content">
        <div class="jurisdiction_content_item">
          <label class="el_lbl">部门名称:</label>
          <el-input class="el_input" size="mini"></el-input>
        </div>
        <div class="jurisdiction_content_item">
          <label class="el_lbl">部门介绍:</label>
          <el-input placeholder="200字内介绍" class="el_input_text_area" type="textarea"
                    :autosize="{ minRows: 3, maxRows: 3}"></el-input>
        </div>
      </div>
      <div class="user_footer" slot="footer">
        <el-row class="row_e">
          <el-button class="btn_deletes" size="small" @click="editVisible = false">取消</el-button>
          <el-button type="primary" size="small" class="btn_delete">保存</el-button>
        </el-row>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import BreadCrumb from '@/components/public/BreadCrumb';
  export default {
    name: "InsideDepartment",
    components: {
      BreadCrumb
    },
    data() {
      return {
        editVisible: false,
        breadData: [{
          id: 1,
          name: '内部管理',
          urls: '/index',
          icon: 'icon-home'
        }, {
          id: 2,
          name: '部门管理',
          urls: '/index/test',
          icon: 'icon-home'
        }],
        tableData: [{
          id: 1,
          departmentName: '技术部',
          description: '负责技术开发',
          createTime: '2019-08-09'
        }],
        jobs: [],
        job: ''
      }
    },
    methods: {
      handleAddJob: function () {
        console.log(this.job)
      }
    }
  }
</script>

<style scoped lang="less">
  .bread {
    margin: 10px;
  }

  .content {
    background: white;
    margin-left: 10px;
    margin-right: 10px;
    height: calc(100vh - 87px);
    width: calc(100vw - 221px);
    border-radius: 2px;
  }

  .title {
    text-align: left;
    padding: 10px;
    padding-left: 20px;
    font-size: 14px;
    border-bottom: 1px solid #f2f2f2;
  }

  .conditions {
    display: flex;
    margin-left: 10px;
    margin-top: 20px;
    justify-content: space-between;
    margin-right: 10px;
    .condition {
      display: flex;
    }
  }

  .role_total {
    background: #409EFF;
    line-height: 28px;
    color: white;
    font-size: 12px;
    margin-right: 20px;
    padding: 0 15px;
  }

  .select_normal {
    width: 150px;
    margin-right: 10px;
  }

  .tabs {
    margin: 20px 10px;
  }

  .dialog_head_title {
    text-align: start;
    font-size: 14px;
    border-bottom: 1px solid #f2f2f2;
    padding-bottom: 10px;
    .examine_icon {
      color: #15bafe;
    }
  }

  .jurisdiction_content {
    .jurisdiction_content_item {
      display: flex;
      line-height: 28px;
      margin-left: 20px;
      margin-bottom: 10px;
      .el_lbl {
        width: 60px;
      }
      .el_input {
        width: 260px;
        margin-left: 20px;
      }
      .el_input1 {
        width: 232px;
        margin-left: 20px;
        flex: 1;
      }
      .el_input_text_area {
        width: 260px;
        margin-left: 20px;
      }
      .add_icon{
        font-size: 28px;
        color: #15bafe;
        cursor: pointer;
      }
    }
    .jurisdiction_item{
      border: 1px solid #f2f2f2;
      height: 100px;
      margin-left: 20px;
    }
  }

  .row_e {
    text-align: center;
    .btn_deletes {
      padding: 5px 15px;
      margin-right: 10px;
    }
    .btn_delete {
      padding: 5px 15px;
    }
  }
</style>
<style>
  .el-table th>.cell{
    text-align: center;
  }
  .el-dialog__body{
    padding: 0 20px !important;
  }
</style>



