<template>
    <div>
      <BreadCrumb class="bread" :breadData="breadData"></BreadCrumb>
      <div class="content">
        <p class="title">活动被修改</p>
        <div class="content_contain">
          <div class="conditions">
            <el-select size="mini" class="select_normal" @change="handleTypeChange" v-model="typeValue" placeholder="活动类型">
              <el-option
                v-for="item in cityOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
            <el-input class="select_normal" placeholder="活动名称" size="mini" suffix-icon="el-icon-search" > </el-input>
        </div>
        <div class="tables">
            <el-table
              :data="tableData"
              border
              :stripe="true"
              style="width: 100%">
              <el-table-column
                prop="id"
                label="ID"
                width="50">
              </el-table-column>
              <el-table-column
                label="活动名称"
                min-width="200">
                <template slot-scope="scope">
                  <span class="row_activity">{{scope.row.activityName}}</span>
                </template>
              </el-table-column>
              <el-table-column
                prop="type"
                label="类型"
                width="80">
              </el-table-column>
              <el-table-column
                prop="activityTime"
                label="活动时间"
                width="120">
              </el-table-column>
              <el-table-column
                prop="endTime"
                label="报名截止时间"
                width="120">
              </el-table-column>
              <el-table-column
                prop="preModificationState"
                label="修改前状态"
                width="80">
              </el-table-column>
              <el-table-column
                prop="modificationTime"
                label="修改时间"
                width="120">
              </el-table-column>
              <el-table-column
                label="操作"
                width="240"
                fixed="right">
                <template slot-scope="scope">
                  <el-button size="small" type="text">预览活动</el-button>
                  <el-button size="small" type="text" >通过</el-button>
                  <el-button size="small" type="text">驳回</el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
    import BreadCrumb from "../public/BreadCrumb";
    export default {
      name: "ActivityModify",
      components: {BreadCrumb},
      data () {
        return {
          identityVisible: false,
          deleteVisible: false,
          remarkVisible: false,
          useticketVisible:false,
          remark: '',
          warmImg: require("../../assets/image/warm.png"),
          breadData: [{
            id: 1,
            name: '活动',
            urls: '/index',
            icon: 'icon-home'
          },{
            id: 2,
            name: '活动被修改',
            urls: '/index/activity-modify',
            icon: 'icon-home'
          }],
          //城市类型
          cityOptions: [{
            value: 1,
            label: '全部'
          }, {
            value: 2,
            label: '长期'
          },{
            value: 3,
            label: '定时'
          }],
          //类型
          typeOptions: [{
            value: 1,
            label: '全部'
          }, {
            value: 2,
            label: '汽车'
          },{
            value: 3,
            label: '摩托'
          },{
            value: 4,
            label: '无人机'
          }],
          levelOptions: [{
            value: 1,
            label: '全部'
          }, {
            value: 2,
            label: '改装'
          }],
          typeValue: null,
          plateValue: null,
          levelValue: null,
          statusValue: null,
          dialogImageUrl: '',
          dialogVisible: false,
          tableData:[{
            id: 1,
            activityName: '如何给汽车加油',
            type: "长期",
            activityTime:"2018-08-20",
            endTime:"2018-08-20",
            preModificationState:"停止报名",
            modificationTime:"2018-08-20",
          },{
            id: 1,
            activityName: '如何给汽车加油',
            type: "长期",
            activityTime:"2018-08-20",
            endTime:"2018-08-20",
            preModificationState:"停止报名",
            modificationTime:"2018-08-20",
          },{
            id: 1,
            activityName: '如何给汽车加油',
            type: "长期",
            activityTime:"2018-08-20",
            endTime:"2018-08-20",
            preModificationState:"停止报名",
            modificationTime:"2018-08-20",
          },{
              id: 1,
            activityName: '如何给汽车加油',
            type: "长期",
            activityTime:"2018-08-20",
            endTime:"2018-08-20",
            preModificationState:"停止报名",
            modificationTime:"2018-08-20",
          }]
        }
      },
      methods: {
        handleTypeChange: function (e) {
          console.log(e,this.typeValue)
        },
        handleRemove(file, fileList) {
          console.log(file, fileList);
        },
        handlePreview(file) {
          console.log(file);
        },
        handleExceed(files, fileList) {
          this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
        },
        beforeRemove(file, fileList) {
          return this.$confirm(`确定移除 ${ file.name }？`);
        },
        handlePictureCardPreview(file) {
          this.dialogImageUrl = file.url;
          this.dialogVisible = true;
        },
        handleExamine: function () {
          this.examineVisible = false;
        },
        handleDelete: function () {
          this.deleteVisible = false;
        }
      }
    }
</script>

<style scoped>
  .bread{
    margin: 10px;
  }
  .content{
    background: white;
    margin-left: 10px;
    margin-right: 10px;
    height: calc(100vh - 87px);
    width: calc(100vw - 221px);
    border-radius: 2px;
  }
  .title{
    text-align: left;
    padding: 10px;
    padding-left: 20px;
    font-size: 14px;
    border-bottom: 1px solid #f2f2f2;
  }
  .conditions{
    display: flex;
    margin-left: 10px;
    margin-top: 20px;
  }
  .select_normal{
    width: 120px;
    margin-right: 10px;
  }
  .tables{
    margin: 20px 10px;
  }
  .row_activity{
    width: 200px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .dialog_head_title{
    height: 16px;
    border-bottom: 1px solid #cccccc;
    padding-bottom: 10px;
    text-align: left;
    font-size: 15px;
  }
  .dialog_delete_head_title{
    height: 16px;
    border-bottom: 1px solid #cccccc;
    padding-bottom: 10px;
    text-align: left;
    font-size: 15px;
  }
  .delete_icon{
    color: #15bafe;
    font-size: 18px;
    top: 1px;
    position: relative;
  }
  .examine_icon{
    color: #15bafe;
  }
  .up_safe{
    height: 120px;
    border: 1px solid #f2f2f2;
  }
  .up_safe_header{
    display: flex;
    line-height: 24px;
    margin-top: 10px;
    position: relative;
    left: -60px;
  }
  .btn_up{
    padding: 5px 10px;
    margin-right: 20px;
  }
  .up_img{
    height: 130px;
    border: 1px solid #f2f2f2;
    margin-top: 10px;
    display: grid;
  }
  .remark{
    text-align: left;
    margin-left: 10px;
  }
  .up_img_header{
    display: flex;
    line-height: 24px;
    margin-top: 10px;
    position: absolute;
    left: 30px;
    width: 250px;
  }
  .up_img_item{
    /*width: 40px;
    height: 40px;*/
    display: -webkit-box;
    overflow-y: auto;
    height: 100px;
    margin-left: 10px;
    margin-top: 10px;
    margin-right: 10px;
    flex-wrap: wrap;
  }
  .icon_plus{
    position: relative;
    bottom: 30px;
  }
  .delete_content{

  }
  .boolean_delete{
    height: 30px;
    width: 30px;
  }
  .delete_content p{
    margin-top: 5px;
  }
  .row_e{
    text-align: center;
  }
  .btn_delete{
    padding: 5px 15px;
    margin-left: 30px;
  }
  .btn_deletes{
    padding: 5px 15px;
  }
  .remark_content{
    position: relative;
    top: -10px;
  }
  .remark_p{
    text-align: left;
  }
  .text_area{
    margin-top: 8px;
  }
  .history_p{
    text-align: left;
    margin-top: 8px;
    border-bottom: 1px solid #f2f2f2;
    padding-bottom: 10px;
  }
  .h_content{
    margin-top: 10px;
    height: 200px;
    overflow-y: auto;
  }
  .history_item{
    display: block;
    text-align: left;
    margin-bottom: 20px;
  }
  .item_top{
    display: flex;
    height: 20px;
    line-height: 20px;
  }
  .point{
    width: 5px;
    height: 5px;
    border-radius: 2.5px;
    background: #15bafe;
    position: relative;
    top: 8px;
    margin-right: 8px;
  }
  .h_name{
    margin-right: 10px;
  }
  .h_time{
    color: #15bafe;
    font-size: 12px;
  }
  .h_message{
    margin-left: 15px;
  }
</style>
<style scoped>
  .el-table th>.cell{
    text-align: center;
  }
  .el-upload-list{
    height: 85px;
    /*overflow: auto;*/
    width:300px;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .el-upload--picture-card{
    width: 90px;
    height: 90px;
  }
  /*.el-upload--picture-card i{
    position: relative;
    bottom: 30px;
  }*/
  .el-upload-list--picture-card .el-upload-list__item{
    width: 90px;
    height: 90px;
  }
  .el-dialog__body{
    padding-top: 15px;
    padding-bottom: 15px;
  }
  .phone{display: flex;align-items:center;justify-content: space-between;margin-top:20px;}
  .phone>div{width: 210px;height: 35px;border:1px solid #e6e6e6;text-align:center;line-height:35px;}
</style>




