<template>
  <div>
    <BreadCrumb class="bread" :breadData="breadData"></BreadCrumb>
    <div class="content">
      <p class="title">用户角色分配</p>
      <div class="conditions">
        <div class="condition">
          <p class="user_name">姓名: <span>大可可</span></p>
          <p class="user_status">状态</p>
          <p class="user_enable" :style="status === 2?{background:'#ff5858'}:{}" @click="handleStatus">{{status===1?'启用':'禁用'}}</p>
        </div>
      </div>
      <div class="user_edit_content">
        <div class="content_left">
          <p class="left_title">所有角色</p>
          <div class="all_role">
            <el-checkbox-group class="checkbox_all" v-model="checkedHasRole" @change="handleCheckedRoleChange">
              <el-checkbox class="checkbox_all_item" v-for="role in allRole" :label="role.id" :key="role.id">{{role.roleName}}</el-checkbox>
            </el-checkbox-group>
          </div>
        </div>
        <div class="content_right">
          <p class="right_title">已拥有角色</p>
          <div class="has_role">
            <el-checkbox-group class="checkbox_all" v-model="checkedHasRole" @change="handleCheckedRoleChange">
              <el-checkbox class="checkbox_all_item" v-for="role in hasRole" :label="role.id" :key="role.id">{{role.roleName}}</el-checkbox>
            </el-checkbox-group>
          </div>
        </div>
      </div>
      <div class="btn_group">
        <el-button size="mini">取消</el-button>
        <el-button size="mini" type="primary">确定</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import BreadCrumb from '@/components/public/BreadCrumb';
export default {
  name: "InsideUserJurisdiction",
  components: {
    BreadCrumb
  },
  data() {
    return {
      status: 1,
      checkedHasRole: [1,2],
      breadData: [{
        id: 1,
        name: '内部管理',
        urls: '/index',
        icon: 'icon-home'
      }, {
        id: 2,
        name: '用户管理',
        urls: '/index/test',
        icon: 'icon-home'
      }, {
        id: 3,
        name: '用户角色权限分配',
        urls: '/index/test',
        icon: 'icon-home'
      }],
      allRole: [{
        id: 1,
        roleName: '超级管理员',
      },{
        id: 2,
        roleName: '普通管理员',
      },{
        id: 3,
        roleName: '视屏管理员',
      },{
        id: 4,
        roleName: '网站管理员',
      }],
      hasRole: [{
        id: 1,
        roleName: '超级管理员',
      },{
        id: 2,
        roleName: '普通管理员',
      }]
    }
  },
  methods: {
    handleStatus:function () {
      if(this.status === 1){
        this.status = 2;
      }else if(this.status === 2) {
        this.status = 1;
      }
    },
    handleCheckedRoleChange:function (value) {
      console.log(value)
    }
  }
}
</script>

<style scoped lang="less">
  .bread {
    margin: 10px;
  }
  .content {
    background: white;
    margin-left: 10px;
    margin-right: 10px;
    height: calc(100vh - 103px);
    width: calc(100vw - 221px);
    border-radius: 2px;
  }
  .title{
    text-align: left;
    padding: 10px;
    padding-left: 20px;
    font-size: 14px;
    border-bottom: 1px solid #f2f2f2;
  }
  .conditions{
    display: flex;
    margin-left: 10px;
    margin-top: 20px;
    justify-content: space-between;
    margin-right: 10px;
    .condition{
      display: flex;
      .user_name{
        font-size: 12px;
        color: white;
        background: #15bafe;
        padding: 5px 15px;
      }
      .user_status{
        font-size: 12px;
        color: white;
        background: #15bafe;
        padding: 5px 15px;
        margin-left: 10px;
      }
      .user_enable{
        font-size: 12px;
        color: white;
        background: #15bafe;
        padding: 5px 15px;
        margin-left: 10px;
      }
      .user_forbid{
        font-size: 12px;
        color: white;
        background: #15bafe;
        padding: 5px 15px;
        margin-left: 10px;
      }
    }
  }
  .user_edit_content{
    display: flex;
    margin: 20px 10px;
    .content_left{
      width: 300px;
      .left_title{
        width: 300px;
        background: #15bafe;
        color: white;
        font-size: 12px;
        height: 28px;
        line-height: 28px;
      }
      .all_role{
        height: 400px;
        border: 1px solid #f2f2f2;
        overflow-y: auto;
        .checkbox_all{
          .checkbox_all_item{
            display: block;
            text-align: left;
            margin-left: 20px !important;
            margin-top: 10px;
          }
        }
      }
    }
    .content_right{
      width: 300px;
      margin-left: 40px;
      .right_title{
        width: 300px;
        background: #15bafe;
        color: white;
        font-size: 12px;
        height: 28px;
        line-height: 28px;
      }
      .has_role{
        height: 400px;
        border: 1px solid #f2f2f2;
        overflow-y: auto;
        .checkbox_all{
          .checkbox_all_item{
            display: block;
            text-align: left;
            margin-left: 20px !important;
            margin-top: 10px;
          }
        }
      }
    }
  }
  .btn_group{
    text-align: left;
    margin-left: 270px;
  }
</style>



