<template>
    <div class="Timed-activity-state">
        <BreadCrumb class="bread" :breadData="breadData"></BreadCrumb>
        <div class="content">
            <div class="title">
                <p>定时活动状态</p>
                <p>
                    <img :src="img" alt="">
                    <span>刷新</span>
                </p>
            </div>
            <div class="content_contain">
                <div class="search-top">
                    <!-- 活动时间 -->
                    <el-select size="mini" class="select_normal" v-model="typeValue" placeholder="活动时间">
                        <el-option v-for="item in TypeOptions" :key="item.value" :label="item.label" :value="item.value" class="hei"></el-option>
                    </el-select>
                    <!-- 报名截止时间 -->
                    <el-select size="mini" class="select_normal" v-model="plateValue" placeholder="报名截止时间">
                        <el-option v-for="item in PlateOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
                    </el-select>
                    <!-- 状态 -->
                    <el-select size="mini" class="select_normal" v-model="StateValue" placeholder="状态">
                        <el-option v-for="item in StateOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
                    </el-select>
                    <!-- 报名人数 -->
                    <el-select size="mini" class="select_normal" v-model="PeopleValue" placeholder="报名人数">
                        <el-option v-for="item in PeopleOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
                    </el-select>
                    <!-- 活动名称 -->
                    <el-input class="select_normal" placeholder="活动名称" size="mini" suffix-icon="el-icon-search" ></el-input>
                </div>
                <div class="tables">
                    <el-table   :data="tableData" border :stripe="true" :header-row-style="{height:'40px'}" :header-cell-style="{padding:0,background:'#15bafe',color:'white'}" :row-style="{height:'40px'}" :cell-style="{padding:0}" style="width: 100%">
                        <el-table-column prop="id" label="ID" width="50"></el-table-column>
                        <el-table-column prop="activityName" label="活动名称" min-width="200"></el-table-column>
                        <el-table-column prop="city" label="城市" width="80"> </el-table-column>
                        <el-table-column prop="companyName" label="公司对外名称" width="200"> </el-table-column>
                        <el-table-column prop="activityTime" label="活动时间" width="100"> </el-table-column>
                        <el-table-column prop="endTime" label="报名截止时间" width="120"></el-table-column>
                        <el-table-column prop="enrolment" label="报名人数" width="80"></el-table-column>
                        <el-table-column prop="ticketsNumber" label="售票数量" width="120"> </el-table-column>
                        <el-table-column prop="visitorsNumber" label="浏览人数" width="120"> </el-table-column>
                        <el-table-column prop="evaluationScore" label="评价分数" width="120"> </el-table-column>
                        <el-table-column prop="state" label="状态" width="120"> </el-table-column>
                        <el-table-column label="操作" width="240" fixed="right">
                            <template slot-scope="scope">
                                <el-button size="small" type="text">
                                    <el-dropdown trigger="click">
                                    <span class="el-dropdown-link">
                                        查看
                                    </span>
                                    <el-dropdown-menu slot="dropdown" type="text">
                                        <el-dropdown-item >
                                            <el-button size="small" type="text" @click="basicInformation = true">基本信息</el-button>
                                        </el-dropdown-item>
                                        <el-dropdown-item>
                                            <el-button size="small" type="text">查看评价</el-button>
                                        </el-dropdown-item>
                                        <el-dropdown-item>
                                            <el-button size="small" type="text">添加备注</el-button>
                                        </el-dropdown-item>
                                    </el-dropdown-menu>
                                    </el-dropdown>
                                </el-button>
                                <el-button size="small" type="text" @click="remarkVisible = true" >添加备注</el-button>
                                <el-button size="small" @click="deleteVisible = true" type="text">删除</el-button>
                                <el-button size="small" type="text" @click="cancelVisible = true">取消活动</el-button>
                                <el-button size="small" type="text" >恢复</el-button>
                                <el-button size="small" type="text">修改</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>
            </div>
        </div>
        <!-- 弹框 -->
        <el-dialog  :visible.sync="basicInformation" width="705px">
            <div slot="title" class="dialog_head_title">
                <i class=" el-icon-document examine_icon"></i>
                <span>提交审核</span>
            </div>
            <!-- <div class="ti-le">
                    <img src="../../assets/image/book.png" alt="">
                    <span>基本信息</span>
            </div> -->
            <div class="info">
                <div>
                    <div class="info-left">
                        <span>活动时间：</span>
                        <div>2018-09-16</div>
                    </div>
                    <div class="info-right">
                        <span>截止时间：</span>
                        <div>2019-09-26</div>
                    </div>
                </div>
                <div>
                    <div class="info-left">
                        <span>板块：</span>
                        <div>汽车</div>
                    </div>
                    <div class="info-right">
                        <span>二级目录:</span>
                        <div>改装</div>
                    </div>
                </div>
                <div>
                    <div class="info-left">
                        <span>城市：</span>
                        <div>重庆</div>
                    </div>
                    <div class="info-right">
                        <span>活动负责人:</span>
                        <div>张三</div>
                    </div>
                </div>
                <div class="p1">
                    <span>详细地址：</span>
                    <div>重庆大坪英利国际2号楼11-5</div>
                </div>
                <div>
                    <div class="info-left">
                        <span>电话：</span>
                        <div>123456</div>
                    </div>
                    <div class="info-right">
                        <span>已结束：</span>
                        <div>2018-09-43</div>
                    </div>
                </div>
            </div>
            <div class="act">
                <div>
                    <img src="../../assets/image/yuanx.png" alt="">
                </div>
                <div>
                    <img src="../../assets/image/fangda.png" alt="">
                    <img src="../../assets/image/yuanx.png" alt="">
                </div>
            </div>
            <div class="preview">
                <div>上传的安全协议</div>
                <div>上传的附件,图片为此公司现场图片，或者计划书等</div>
            </div>
        </el-dialog>
        <!-- 取消活动原因弹窗 -->
        <el-dialog :visible.sync="cancelVisible" width="360px">
            <div slot="title" class="dialog_head_title">
                <i class=" el-icon-delete examine_icon"></i>
                <span>取消活动原因</span>
            </div>
            <div class="reject_content">
                <el-input type="textarea" :rows="2" placeholder="请输入取消活动原因" v-model="textarea"></el-input>
            </div>
            <div class="tips">
                取消活动后系统将自动退款给用户，此操作不可逆。
            </div>
            <div class="user_footer" slot="footer">
                <el-row class="row_e">
                <el-button class="btn_deletes" size="small" @click="cancelVisible = false">取消</el-button>
                <el-button type="primary" size="small"  class="btn_delete">提交</el-button>
                </el-row>
            </div>
        </el-dialog>
        <!-- 添加备注弹窗 -->
        <el-dialog :visible.sync="remarkVisible" width="400px">
            <div slot="title" class="dialog_delete_head_title">
                <i class="iconfont icon-edit-square delete_icon"></i>
                <span>添加备注</span>
            </div>
            <div class="remark_content">
            <p class="remark_p">内容</p>
            <el-input
                type="textarea"
                :rows="2"
                class="text_area"
                placeholder="商家的修改，取消，结束活动都将显示在历史里面"
                v-model="remark">
            </el-input>
            <p class="history_p">历史</p>
            <div class="h_content">
                <div class="history_item" v-for="(item,index) in historyData" :key="index">
                <div class="item_top">
                    <div class="point"/>
                    <p class="h_name">{{item.name}}</p>
                    <p class="h_time">{{item.createTime}}</p>
                </div>
                <p class="h_message">{{item.message}}</p>
                </div>
            </div>
            </div>
            <div class="user_footer" slot="footer">
            <el-row class="row_e">
                <el-button class="btn_deletes" size="small" @click="remarkVisible = false">取消</el-button>
                <el-button type="primary" size="small"  class="btn_delete" >提交</el-button>
            </el-row>
            </div>
        </el-dialog>
        <!-- 删除弹窗 -->
        <el-dialog :visible.sync="deleteVisible" width="300px">
            <div slot="title" class="dialog_delete_head_title">
                <i class="iconfont icon-trash delete_icon"></i>
                <span>删除</span>
            </div>
            <div class="delete_content">
                <img class="boolean_delete" :src="warmImg"/>
                <p>确定删除吗？</p>
            </div>
            <div class="user_footer" slot="footer">
            <el-row class="row_e">
                <el-button class="btn_deletes" size="small" @click="deleteVisible = false">取消</el-button>
                <el-button type="primary" size="small"  class="btn_delete" >提交</el-button>
            </el-row>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import BreadCrumb from "../public/BreadCrumb";
    export default {
        name: "ActivityTimeState",
        components: {BreadCrumb},
        data (){
            return {
                textarea:"",
                warmImg: require("../../assets/image/warm.png"),
               img: require('../../assets/image/new.png'),
               //弹框
               basicInformation:false,
               cancelVisible:false,
               remarkVisible:false,
               deleteVisible:false,
               remark:"",
                //标签栏
                 breadData: [{
                    id: 1,
                    name: '活动',
                    urls: '/index',
                    icon: 'icon-home'
                },{
                    id: 2,
                    name: '定时活动状态',
                    urls: '/index/test',
                    icon: 'icon-home'
                }],
               gridData: [{
                    date: '2016-05-02',
                    name: '王小虎',
                    address: '上海市普陀区金沙江路 1518 弄'
                }, {
                    date: '2016-05-04',
                    name: '王小虎',
                    address: '上海市普陀区金沙江路 1518 弄'
                }, {
                    date: '2016-05-01',
                    name: '王小虎',
                    address: '上海市普陀区金沙江路 1518 弄'
                }, {
                    date: '2016-05-03',
                    name: '王小虎',
                    address: '上海市普陀区金沙江路 1518 弄'
                }],
               //选择框显示的值
               typeValue: null,plateValue:null,StateValue:null,PeopleValue:null,
            //    选择下拉框的值
               TypeOptions: [{
                    value: 1,
                    label: '从早到晚'
                }, {
                    value: 2,
                    label: '从晚到早'
                }],
                PlateOptions: [{
                    value: 1,
                    label: '从早到晚'
                }, {
                    value: 2,
                    label: '从晚到早'
                }],
                    StateOptions: [{
                    value: 1,
                    label: '已结束'
                }, {
                    value: 2,
                    label: '报名中'
                }],
                 PeopleOptions: [{
                    value: 1,
                    label: '10-20人'
                }, {
                    value: 2,
                    label: '20-50人'
                }],
                // 数据
                tableData: [{
                    id: 1,
                    activityName: '如何给汽车加油',
                    city:'重庆',
                    companyName: '酷耍电子商务',
                    activityTime:'2018.9.29',
                    endTime:'2018.10.01',
                    enrolment:'45',
                    ticketsNumber:'99',
                    visitorsNumber:'123',
                    evaluationScore:'100',
                    state:'已结束',
                   
                },{
                   id: 2,
                    activityName: '如何给汽车加油',
                    city:'重庆',
                    companyName: '酷耍电子商务',
                    activityTime:'2018.9.29',
                    endTime:'2018.10.01',
                    enrolment:'555',
                    ticketsNumber:'99',
                    visitorsNumber:'123',
                    evaluationScore:'100',
                    state:'已结束',
                },{
                   id: 3,
                    activityName: '如何给汽车加油',
                    city:'重庆',
                    companyName: '酷耍电子商务',
                    activityTime:'2018.9.29',
                    endTime:'2018.10.01',
                    enrolment:'45',
                    ticketsNumber:'99',
                    visitorsNumber:'123',
                    evaluationScore:'100',
                    state:'报名中',
                }],
                historyData: [{
                    id: 1,
                    name: '康宇',
                    createTime: '2018/08/07',
                    message: '联系了对方愿意长期合作'
                },{
                    id: 2,
                    name: '康宇',
                    createTime: '2018/08/07',
                    message: '联系了对方愿意长期合作联系了对方愿意长期合作联系了对方愿意长期合作联系了对方愿意长期合作'
                },{
                    id: 3,
                    name: '康宇',
                    createTime: '2018/08/07',
                    message: '联系了对方愿意长期合作'
                },{
                    id: 4,
                    name: '康宇',
                    createTime: '2018/08/07',
                    message: '联系了对方愿意长期合作'
                }]
            }
        },
        mounted(){
          
        },
        methods:{
           
        }
    }
</script>

<style scoped>
    .bread{margin: 10px;}
    .Timed-activity-state{padding: 0 15px; box-sizing: border-box;height: calc(100vh - 87px);width: calc(100vw - 221px);}
    .content{background: #fff;border-radius: 8px; height: calc(100vh - 87px); width: calc(100vw - 221px);}
    .title{text-align: start;height: 70px;line-height: 70px;border-bottom: 2px solid #f2f2f2;padding:0 50px;display: flex;justify-content: space-between;color: #222222;font-size: 20px;}
    .content_contain{padding: 25px 45px;}
    .search-top{text-align: start;}
    .select_normal{width: 150px;margin-right: 10px;}
    .tables{margin-top: 23px;}
    .el-dropdown-link{font-size: 14px;color: #29bdfe;}
    .el-button+.el-button{font-size: 14px;color: #29bdfe;}
    /* 基本信息弹框 */
    .ti-le{text-align: start;border-bottom: 1px solid #e8e8e8;padding-bottom: 18px;display: flex;align-items: center;}
    .ti-le>img{margin-right: 6px;}
    .info>div{display: flex;justify-content: space-around;margin-top: 27px;}
    .info-left{display: flex;align-items: center;width: 46%;justify-content: space-between;}
    .info-right{display: flex;align-items: center;width: 46%;justify-content: space-between}
    .info-left>div{width: 210px;height: 35px;border: 1px solid #ededed;text-align: center;line-height: 35px}
    .info-right>div{width: 210px;height: 35px;border: 1px solid #ededed;text-align: center;line-height: 35px;}
    .p1{display: flex;align-items: center;}
    .p1>div{width: 540px;height: 35px;border: 1px solid #ededed;text-align: center;line-height: 35px}
    .act{display: flex;justify-content: space-between;margin-top: 25px;}
    .act>div{width: 320px;text-align: right;margin-right: 6px;}
    .preview{display: flex;justify-content: space-between;}
    .preview>div{width: 320px;height: 157px;border: 1px solid #ededed;margin-bottom: 35px;line-height: 157px;color: #aaaaaa;}
    .el-dialog__header{padding: 8px 20px 10px !important;}
    .dialog_head_title{ height: 16px; border-bottom: 1px solid #cccccc;  padding-bottom: 10px; text-align: left; font-size: 15px; }
    .examine_icon{ color: #15bafe; } 
    .tips{width: 318px;height: 60px;border: 1px solid #e8e8e8;padding: 10px 60px;box-sizing: border-box;font-size: 14px;color: #b7b7b7;margin-top: 17px;}
    .btn_deletes{ padding: 5px 15px; } 
    .remark_content{ position: relative; top: -10px; }  
    .remark_p{ text-align: left; }
    .text_area{ margin-top: 8px; }
    .history_p{ text-align: left; margin-top: 8px; border-bottom: 1px solid #f2f2f2; padding-bottom: 10px; }
    .h_content{ margin-top: 10px; height: 200px;  overflow-y: auto; } 
    .history_item{  display: block; text-align: left;  margin-bottom: 20px; }  
    .item_top{ display: flex; height: 20px;  line-height: 20px; }  
    .point{ width: 5px;  height: 5px; border-radius: 2.5px; background: #15bafe;  position: relative;  top: 8px; margin-right: 8px; } 
    .h_name{  margin-right: 10px; }
    .h_time{  color: #15bafe;  font-size: 12px; }
    .h_message{ margin-left: 15px; }
    .el-button--text{color: #b7b7b7;}
      .dialog_head_title{
    height: 16px;
    border-bottom: 1px solid #cccccc;
    padding-bottom: 10px;
    text-align: left;
    font-size: 15px;
  }
  .dialog_delete_head_title{
    height: 16px;
    border-bottom: 1px solid #cccccc;
    padding-bottom: 10px;
    text-align: left;
    font-size: 15px;
  }
  .delete_icon{
    color: #15bafe;
    font-size: 18px;
    top: 1px;
    position: relative;
  }
  .examine_icon{
    color: #15bafe;
  }
  .up_safe{
    height: 120px;
    border: 1px solid #f2f2f2;
  }
  .up_safe_header{
    display: flex;
    line-height: 24px;
    margin-top: 10px;
    position: relative;
    left: -60px;
  }
  .btn_up{
    padding: 5px 10px;
    margin-right: 20px;
  }
  .up_img{
    height: 130px;
    border: 1px solid #f2f2f2;
    margin-top: 10px;
    display: grid;
  }
  .remark{
    text-align: left;
    margin-left: 10px;
  }
  .up_img_header{
    display: flex;
    line-height: 24px;
    margin-top: 10px;
    position: absolute;
    left: 30px;
    width: 250px;
  }
  .up_img_item{
    display: -webkit-box;
    overflow-y: auto;
    height: 100px;
    margin-left: 10px;
    margin-top: 10px;
    margin-right: 10px;
    flex-wrap: wrap;
  }
  .icon_plus{
    position: relative;
    bottom: 30px;
  }
  .boolean_delete{
    height: 30px;
    width: 30px;
  }
  .delete_content p{
    margin-top: 5px;
  }
  .row_e{
    text-align: center;
  }
  .btn_delete{
    padding: 5px 15px;
    margin-left: 30px;
  }
  .btn_deletes{
    padding: 5px 15px;
  }
</style>



